.gs-testimonials + .gs-compare.gs-compare_with-rounded-bg {
    @media (max-width: 767px) {
        padding-top: 0;
    }
}

.gs-testimonials {
    $tm: &;
    
    padding-top: rem(62);
    padding-bottom: math-clamp(70, 76);
    overflow: hidden;
    background-color: $lightgrey;
    
    &_background-transparent {
        background-color: transparent;
    }
    
    .swiper {
        overflow: visible;
        width: calc(100% + #{rem(27)});
        @media (max-width: 767px) {
            overflow: hidden;
            padding: 0 12px 12px 12px;
            margin: 0 -12px;
            width: calc(100% + 24px);
        }
        
        &-controls {
            justify-content: center;
            @media (max-width: 767px) {
                padding: 0 23px;
                justify-content: space-between;
            }
        }
        
        &-slide {
            &:nth-child(even) {
                padding-top: rem(119);
                @media (max-width: 767px) {
                    padding-top: 0;
                }
                
                #{$tm} {
                    &__poster {
                        border-color: $green;
                    }
                }
            }
            
            &:nth-child(3n) {
                padding-top: rem(59);
                @media (max-width: 767px) {
                    padding-top: 0;
                }
                
                #{$tm} {
                    &__poster {
                        border-color: $salmon;
                    }
                }
            }
        }
        
        &-navigation {
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            @media (max-width: 767px) {
                position: static;
                transform: translateY(0);
            }
        }
    }
    
    &__card {
        margin-right: rem(27);
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: math-clamp(31, 35) math-clamp(23, 52) math-clamp(31, 35);
        min-height: rem(350);
        background-color: $white;
        box-shadow: rem(1) rem(1) rem(15) rgba($black2, 0.1);
        border-radius: rem(32);
        
        @media (max-width: 767px) {
            margin-right: 0;
            min-height: 390px;
        }
    }
    
    &__start {
        width: math-clamp(75, 100);
        @media (max-width: 991px) {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            margin-bottom: 18px;
            width: 100%;
        }
        
        & > * {
            @media (max-width: 991px) {
                margin-right: 13px;
            }
        }
    }
    
    &__end {
        width: calc(100% - #{rem(137)});
        @media (max-width: 991px) {
            width: 100%;
        }
    }
    
    &__logo {
        margin-bottom: math-clamp(0, 23);
        max-width: unset;
        width: 100%;
        @media (max-width: 991px) {
            max-width: 125px;
            order: 1;
        }
    }
    
    &__poster {
        width: math-clamp(75, 100);
        height: math-clamp(75, 100);
        border: rem(4) solid $highlighter;
        border-radius: 50%;
        overflow: hidden;
        @media (max-width: 991px) {
            min-width: 75px;
        }
    }
    
    &__person {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__phrase {
        margin: 0 0 rem(15);
        padding: 0;
        color: $black;
    }
    
    &__info {
        margin-bottom: rem(10);
        font-weight: 350;
        font-size: math-clamp(12, 14);
        line-height: 1.6;
    }
}
