.gs-inner-content-with-form {
  position: relative;
  z-index: 1;
  margin-bottom: math-clamp(82, 124);

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 rem(10);
    @media (max-width: 767px) {
      padding: 0;
    }
  }

  &__start {
    width: 39%;
    @media (max-width: 991px) {
      width: 100%;
    }
  }

  &__end {
    padding-right: rem(70);
    width: 47%;
    @media (max-width: 991px) {
      padding-right: 0;
      width: 100%;
    }
  }

  &__external-form {
    margin-top: rem(-144);
    @media (max-width: 991px) {
      margin-top: 0;
    }
  }

  &__textual {
    h3 {
      padding: 0;
      margin: 0 0 rem(7) 0;
    }
  }
}