.gs-advantages {
    $adv: &;
    
    position: relative;
    padding: math-clamp(44, 127) 0 math-clamp(77, 119);
    background-color: $white;
    @media (max-width: 767px) {
        padding-top: 0;
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        @media (max-width: 767px) {
            display: none;
        }
    }
    
    &__left {
        width: 50%;
        height: 100%;
        background-color: $lightgrey;
        border-radius: 0 math-clamp(250, 500) math-clamp(250, 500) 0;
    }
    
    &__content {
        position: relative;
        display: flex;
        flex-wrap: wrap;
    }
    
    &__start {
        width: 50%;
        @media (max-width: 767px) {
            margin: 0 -36px 39px;
            padding: 44px 36px 95px;
            width: calc(100% + 72px);
            border-radius: 0 0 250px 250px;
            background-color: $lightgrey;
            text-align: center;
        }
    }
    
    &__end {
        padding-left: rem(69);
        width: 50%;
        @media (max-width: 991px) {
            padding-left: sd(69);
        }
        @media (max-width: 767px) {
            padding-left: 0;
            width: 100%;
        }
    }
    
    &__title {
        padding: 0;
        margin: 0 0 math-clamp(22, 33);
    }
    
    &__textual {
        max-width: rem(394);
        @media (max-width: 767px) {
            margin: 0 auto;
        }
    }
    
    &__text {
        margin-bottom: math-clamp(36, 50);
        font-weight: 400;
        font-size: math-clamp(14, 16);
        line-height: 1.6;
        
        p {
            &:nth-child(n+2) {
                margin-top: math-clamp(20, 30);
            }
        }
    }
    
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        
        @media (max-width: 767px) {
            justify-content: center;
        }
        
        & > * {
            margin-bottom: math-clamp(24, 48);
        }
    
        #{$adv} {
            &__item {
                &.active {
                    #{$adv} {
                        &__icon {
                            path {
                                stroke: $salmon;
                            }
                        }
                    
                        &__line {
                            width: rem(92);
                            background-color: $salmon;
                        }
                    }
                }
            }
        }
    }
    
    &__item {
        flex: 0 1 rem(250);
        cursor: pointer;
        
        @media (max-width: 767px) {
            text-align: center;
        }
        
        @media (hover: hover) {
            &:hover {
                #{$adv} {
                    &__icon:not(.gs-advantages__icon_fill) {
                        path {
                            stroke: $salmon;
                        }
                    }
                    
                    &__icon_fill {
                        path {
                            fill: $salmon;
                        }
                    }
                    
                    &__line {
                        width: rem(92);
                        background-color: $salmon;
                    }
                }
            }
        }
        
    }
    
    &__icon {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: rem(20);
        padding-bottom: rem(12);
        @media (max-width: 767px) {
            justify-content: center;
        }
        
        svg {
            max-width: rem(50);
            height: auto;
            
            path {
                @extend %transition;
            }
        }
    }
    
    &__line {
        @extend %transition;
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: rem(15);
        height: rem(2);
        background-color: $green;
        @media (max-width: 767px) {
            left: 50%;
            transform: translateX(-50%);
        }
    }
    
    &__name {
        margin: 0 0 rem(12);
        padding: 0;
        font-weight: 700;
        font-size: rem(16);
        color: $coregreen;
        line-height: 1.5;
    }
    
    &__description {
        @extend %transition;
        height: 0;
        overflow: hidden;
        font-weight: 400;
        font-size: rem(16);
        color: $black;
        line-height: 1.6;
        @media (max-width: 767px) {
            height: auto;
        }
    }
}
