.gs-text-with-icons {
    $twi: &;
    
    margin-bottom: math-clamp(9, 83);
    padding-top: math-clamp(44, 70);
    
    &__header {
        margin: 0 auto rem(39);
        max-width: rem(900);
    }
    
    &__title {
        margin-bottom: math-clamp(22, 28);
        text-align: center;
    }
    
    &__subtitle {
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.5;
        text-align: center;
    }
    
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        //align-items: center;
        gap: math-clamp(30, 115);
    
        #{$twi} {
            &__item {
                &.active {
                    #{$twi} {
                        &__icon {
                            path {
                                stroke: $salmon;
                            }
                        }
                    
                        &__line {
                            width: rem(92);
                            background-color: $salmon;
                        }
                    }
                }
            }
        }
    }
    
    &__item {
        cursor: pointer;
        width: 100%;
        
        @media (min-width: 576px) {
            flex: 0 0 210px;
            max-width: 210px;
        }
        
        @media (hover: hover) {
            &:hover {
                #{$twi} {
                    &__img:not(.gs-text-with-icons__img_fill) {
                        path {
                            stroke: $salmon;
                        }
                    }
                    
                    &__img_fill {
                        path {
                            fill: $salmon;
                        }
                    }
                    
                    &__line {
                        width: math-clamp(56, 56);
                        background-color: $salmon;
                    }
                    
                    &__name {
                        svg {
                            opacity: 1;
                            left: -20px;
                        }
                    }
                }
            }
        }
        
        &.active {
            #{$twi} {
                &__img:not(.gs-text-with-icons__img_fill) {
                    path {
                        stroke: $salmon;
                    }
                }
        
                &__img_fill {
                    path {
                        fill: $salmon;
                    }
                }
        
                &__line {
                    width: math-clamp(56, 56);
                    background-color: $salmon;
                }
        
                &__name {
                    svg {
                        opacity: 1;
                        left: -20px;
                    }
                }
            }
        }
    }
    
    &__img {
        position: relative;
        display: flex;
        justify-content: center;
        margin-bottom: rem(15);
        //padding-bottom: math-clamp(14, 16);
        
        svg {
            max-width: 100%;
            width: auto;
            height: rem(42);
            
            path {
                @extend %transition;
            }
        }
    }
    
    /*&__line {
         @extend %transition;
         position: absolute;
         bottom: 0;
         left: 50%;
         transform: translateX(-50%);
         width: rem(15);
         height: rem(2);
         background-color: $green;
     }*/
    
    &__name {
        padding: 0;
        margin: 0;
        color: $coregreen;
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        
        span {
            display: block;
            position: relative;
        }
        
        svg {
            position: absolute;
            top: 8px;
            left: -30px;
            opacity: 0;
            transition: all .5s;
        }
    }
    
    &__description {
        @extend %transition;
        height: 0;
        overflow: hidden;
        font-weight: 400;
        font-size: rem(16);
        text-align: center;
        color: $black;
        line-height: 1.6;
        margin-top: 20px;
        @media (max-width: 767px) {
            height: auto;
        }
    }
}
