.gs-resources-hero {
    $rh: &;
    
    margin-bottom: math-clamp(40, 100);
    visibility: hidden;
    
    &_with-poster {
        #{$rh} {
            &__content {
                padding-bottom: rem(120);
                min-height: rem(363);
            }
            
            &__poster {
                display: block;
            }
        }
    }
    
    &_questions {
        #{$rh} {
            &__content {
                padding-bottom: 40px;
                min-height: math-clamp(360, 525);
            }
        }
    }
    
    &__wr {
        @media (min-width: 1200px) {
            padding: 0 rem(10);
        }
    }
    
    &__content {
        position: relative;
        padding: rem(42) 0 rem(39);
        color: $white;
        min-height: rem(444);
        
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $coregreen;
        }
        
        .gs-container {
            min-height: inherit;
        }
    }
    
    &__textual {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: inherit;
    }
    
    &__top {
        position: relative;
    }
    
    &__mid {
        position: relative;
        padding: 0 rem(10);
        margin: rem(20) 0;
        text-align: center;
        @media (max-width: 767px) {
            padding: 0;
        }
    }
    
    &__bot {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 rem(10);
        @media (max-width: 767px) {
            flex-direction: column;
            
            & > *:nth-child(n+2) {
                margin-top: 20px;
            }
        }
    }
    
    &__back {
        display: flex;
        flex-direction: column;
        font-weight: 600;
        font-size: math-clamp(14, 16);
        color: $white;
        line-height: 1.9;
        text-decoration: none;
        
        path {
            @extend %transition;
        }
        
        @media (hover: hover) {
            &:hover {
                path {
                    stroke: $salmon;
                }
            }
        }
    }
    
    &__title {
        padding: 0;
    }
    
    &__category {
        margin-bottom: rem(13);
        font-weight: 700;
        font-size: math-clamp(14, 16);
        line-height: 1.4;
    }
    
    &__info {
        font-weight: 350;
        font-size: math-clamp(14, 16);
        line-height: 1.4;
        text-align: center;
    }
    
    &__poster {
        position: relative;
        z-index: 1;
        display: none;
        margin-top: rem(-100);
        border-radius: rem(4);
        overflow: hidden;
        
        @media (max-width: 767px) {
            padding: 0;
        }
        
        &_blog {
            img {
                width: 100%;
                height: math-clamp(260, 575);
                object-fit: cover;
            }
        }
    }
    
    &__logos {
        display: flex;
        flex-wrap: wrap;
        margin: rem(50) 0 0 0;
        padding: 0;
        list-style: none;
        
        li {
            display: flex;
            align-items: center;
            margin-right: rem(19);
            padding-right: rem(19);
            border-right: rem(2) solid $grey;
            
            &:last-child {
                margin-right: 0;
                padding-right: 0;
                border-right: 0;
            }
        }
    }
}
