.gs-filter {
    position: relative;
    display: flex;
    justify-content: center;
    z-index: 1;
    filter: drop-shadow(rem(4) rem(4) rem(25) rgba($black2, 0.15));
    will-change: filter;
    @media (max-width: 991px) {
        filter: none;
    }
    
    &__list {
        display: inline-flex;
        // gap: 23px;
        margin: 0 auto;
        padding: rem(9) rem(14);
        background-color: $white;
        border-radius: rem(70);
        @media (max-width: 991px) {
            display: none;
        }
        
        & > *:nth-child(n+2) {
            margin-left: rem(23);
        }
        
        label {
            cursor: pointer;
            display: block;
            
            input {
                display: none;
            }
            
            span {
                @extend %transition;
                @extend %green-btn;
                display: block;
                max-width: 100%;
                width: auto;
                border-radius: rem(70);
                background-color: transparent;
                color: $coregreen;
                min-width: unset;
    
                &:hover {
                    background-color: $coregreen;
                    color: $white;
                }
            }
    
            input:checked + span {
                background-color: $coregreen;
                color: $white;
                pointer-events: none;
            }
        }
    }
    
    &__btn {
        max-width: 100%;
        width: auto;
        border-radius: rem(70);
        background-color: transparent;
        color: $coregreen;
        min-width: unset;
        
        &:hover {
            background-color: $coregreen;
            color: $white;
        }
        
        &.active {
            background-color: $coregreen;
            color: $white;
            pointer-events: none;
        }
    }
    
    .custom-select {
        &-container {
            display: none;
            min-width: rem(264);
            max-width: 100%;
            filter: drop-shadow(rem(4) rem(4) rem(25) rgba($black2, 0.15));
            will-change: filter;
            @media (max-width: 991px) {
                display: block;
            }
            
            &.is-open {
                .custom-select {
                    &-panel {
                        max-height: unset;
                    }
                    
                    &-opener {
                        &:after {
                            transform: translateY(-50%) rotate(-180deg);
                        }
                    }
                }
            }
        }
        
        &-opener {
            position: relative;
            z-index: 2;
            display: flex;
            align-items: center;
            padding: 0 rem(87) 0 rem(35);
            max-width: 100%;
            height: rem(85);
            background-color: $white;
            border-radius: rem(145);
            font-weight: 700;
            font-size: rem(16);
            color: $coregreen;
            line-height: 1;
            z-index: 2;
            
            &::after {
                content: '';
                position: absolute;
                top: 50%;
                right: rem(26);
                transform: translateY(-50%);
                width: rem(32);
                height: rem(32);
                background-color: $coregreen;
                border-radius: 50%;
                background-image: url('../img/filter-arrow.svg');
                background-repeat: no-repeat;
                background-position: center;
                background-size: rem(11) rem(6);
                transform-origin: center;
            }
        }
        
        &-panel {
            transform: translateY(rem(-43));
            background: $white;
            border-radius: 0 0 rem(42) rem(42);
            font-weight: 700;
            font-size: rem(16);
            color: $coregreen;
            line-height: 1;
        }
        
        &-option {
            padding: rem(14) rem(35);
            cursor: pointer;
            
            &::before {
                display: none;
            }
            
            &:first-child {
                padding-top: rem(70);
            }
            
            &:last-child {
                padding-bottom: rem(33);
            }
            
            &.has-focus {
                background-color: transparent;
            }
            
            &.is-selected {
                pointer-events: none;
            }
        }
    }
}
