.gs-featured-slider {
  position: relative;
  @media (max-width: 767px) {
    margin-top: 50px;
    margin-bottom: 130px;
  }

  .swiper {
    &-controls {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 20px;
    }

    &-slide {
      @extend %transition;
      opacity: 0 !important;

      &-active {
        opacity: 1 !important;
      }
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
  }

  &__left {
    padding-top: math-max(29, 96);
    width: 50%;
    height: 100%;
    @media (max-width: 767px) {
      margin-top: 50px;
      padding-top: 0;
      width: 100%;
      height: 300px;
    }
  }

  &__circle {
    width: 100%;
    height: 100%;
    background: $green;
    border-radius: 0 0 0 100%;
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__start {
    margin-bottom: math-max(29, 96);
    width: 46%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__end {
    margin-top: math-max(29, 96);
    width: 46%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__poster {
    position: relative;
    width: math-max(315, 550);
    max-width: 100%;
    border-radius: rem(4);
    overflow: hidden;
    @media (max-width: 767px) {
      width: 100%;
    }

    &::after {
      content: '';
      display: block;
      padding-bottom: 100%;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__category {
    margin-bottom: math-clamp(29, 38);
    font-weight: 700;
    font-size: 16px;
    color: $coregreen;
    line-height: 1.4;
  }

  &__name {
    margin: 0 0 rem(8);
    padding: 0;
    @media (max-width: 767px) {
      margin-bottom: 29px;
    }
  }

  &__textual {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: rem(20);
    padding-top: rem(35);
    height: 100%;
    @media (max-width: 767px) {
      padding-top: 70px;
    }
  }

  &__text {
    margin-bottom: math-clamp(20, 33);
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
            line-clamp: 4; 
    -webkit-box-orient: vertical;
  }
}