.gs-resources-section {
    $ln: &;
    
    position: relative;
    padding: math-clamp(48, 112) 0 math-clamp(94, 109);
    
    &_questions {
        margin-top: 50px;
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    
    &__circle {
        content: '';
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $lightgrey;
        border-radius: 50% / 100%;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        @media (max-width: 991px) {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            width: rem(1018);
            border-radius: 2000px 2000px 0 0;
        }
        @media (max-width: 767px) {
            width: 818px;
        }
    }
    
    &__content {
        position: relative;
        z-index: 1;
    }
    
    &__header {
        margin-bottom: math-clamp(77, 77);
    }
    
    &__textual {
        margin: 0 auto;
        max-width: rem(900);
        text-align: center;
    }
    
    &__title {
        margin: 0;
    }
    
    &__text {
        margin-top: rem(20);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.5;
    }
    
    &__more {
        margin-top: rem(25);
        @media (max-width: 767px) {
            margin-top: 48px;
        }
    }
    
    &_with-circle {
        padding-top: math-clamp(126, 210);
        overflow: hidden;
        
        #{$ln} {
            &__circle {
                display: block;
            }
        }
    }
    
    &__justify {
        &-header {
            display: none;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-bottom: math-clamp(28, 52);
            padding-bottom: math-clamp(49, 90);
            border-bottom: rem(2) solid $grey;
        }
        
        &-start {
            width: 50%;
            @media (max-width: 767px) {
                margin-bottom: 29px;
                width: 100%;
            }
        }
        
        &-end {
            width: 50%;
            @media (max-width: 767px) {
                width: 100%;
            }
        }
        
        &-text {
            margin-bottom: math-clamp(36, 43);
            font-weight: 400;
            font-size: math-clamp(18, 24);
            color: $black;
            line-height: 1.5;
        }
    }
    
    &__carousel {
        margin-bottom: math-clamp(40, 100);
    
        .slick {
            &-track {
                display: flex;
                align-items: center;
            }
        }
        
        &-wr {
            padding: 0 math-clamp(28, 63);
        }
    
        &-img {
            max-width: math-clamp(88, 188);
            filter: grayscale(1);
        }
    }
    
    &_with-justify-header {
        background-color: $lightgrey;
        
        #{$ln} {
            &__header {
                @media (max-width: 767px) {
                    margin-bottom: 0;
                }
            }
            
            &__textual {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                margin-bottom: math-clamp(28, 52);
                padding-bottom: math-clamp(38, 90);
                border-bottom: rem(2) solid $grey;
                max-width: 100%;
                text-align: left;
            }
            
            &__start {
                width: 50%;
                @media (max-width: 767px) {
                    margin-bottom: 29px;
                    width: 100%;
                }
            }
            
            &__end {
                width: 45%;
                @media (max-width: 767px) {
                    width: 100%;
                }
            }
            
            &__more {
                @media (max-width: 767px) {
                    margin-top: 36px;
                }
            }
            
            &__text {
                @media (max-width: 767px) {
                    margin-top: 0;
                }
            }
        }
    }
    
    &_logos {
        #{$ln} {
            &__header {
                margin-bottom: 0;
            }
    
            &__textual {
                border-bottom: none;
            }
        }
    }
}
