.gs-accordion-content {
    overflow: hidden;
    
    @media (max-width: 1199px) {
        padding-bottom: 50px;
    }
    
    &__container {
        width: calc(100% - 60px);
        max-width: 1190px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: math-clamp(40, 100);
    }
    
    &__text-container {
        min-height: math-clamp(340, 720);
        padding: 60px 0;
        position: relative;
        flex: 0 0 100%;
        max-width: 100%;
    
        @media (min-width: 768px) {
            padding: math-clamp(60, 145) math-clamp(70, 140) math-clamp(60, 135) 0;
        }
        @media (min-width: 1200px) {
            flex: 0 0 math-clamp(500, 595);
            max-width: math-clamp(500, 595);
        }
        
        &:before {
            content: '';
            position: absolute;
            top: 0;
            right: -30px;
            height: 100%;
            width: 120vw;
            background-color: $coregreen;
            z-index: 1;
    
            @media (min-width: 768px) {
                right: 0;
                border-radius: 0 500px 500px 0;
            }
        }
    }
    
    &__text {
        position: relative;
        z-index: 5;
        color: $white;
    }
    
    &__content {
        flex: 0 0 100%;
        max-width: 100%;
        
        @media (min-width: 1200px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
        }
        
        .ui-accordion-header {
            color: $coregreen;
            margin-bottom: 7px;
            cursor: pointer;
            line-height: 32px;
            position: relative;
            padding-top: 24px;
            margin-top: 24px;
            border-top: 2px solid $grey;
            
            &:first-child {
                margin-top: 0;
                padding-top: 0;
                border-top: none;
    
                .ui-accordion-header-icon {
                    top: 50%;
                }
            }
            
            .gs-accordion-content__accordion-number {
                color: $green;
                padding-right: 7px;
            }
            
            &.ui-accordion-header-active {
                .ui-accordion-header-icon {
                    transform: translateY(-50%) rotate(180deg);
                }
            }
            
            .ui-accordion-header-icon {
                position: absolute;
                top: calc(50% + 12px);
                right: 0;
                transform: translateY(-50%) rotate(0);
                width: 32px;
                height: 32px;
                border-radius: 50%;
                background-color: $coregreen;
                transition: all .4s;
                
                &:before {
                    content: '';
                    position: absolute;
                    width: 10px;
                    height: 6px;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: contain;
                    background-image: url(../img/arrow-accordion.svg);
                }
            }
        }
    }
    
    &__accordion-text {
        max-width: 440px;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        color: $black;
    }
}
