.gs-share {
  display: flex;
  align-items: center;
}
.gs-share > span {
  margin-right: 1.4375rem;
  font-weight: 600;
  font-size: clamp(0.875rem, 1.1111111111vw, 1rem);
  line-height: 1;
}
@media (max-width: 767px) {
  .gs-share > span {
    display: none;
  }
}
.gs-share__socials {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}
.gs-share__socials .menu-item:nth-child(n+2) {
  margin-left: 1.25rem;
}
@media (hover: hover) {
  .gs-share__socials .menu-item a:hover path {
    fill: #FF9B74;
  }
}
.gs-share_green {
  color: #0B4F41;
}
.gs-share_green path {
  fill: #0B4F41;
}