.gs-contact-block-2 {
    padding: math-clamp(83, 119) 0 math-clamp(88, 126);
    position: relative;
    z-index: 6;
    
    &__content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    
    &__start {
        padding-bottom: math-clamp(83, 120);
        width: 46%;
        @media (max-width: 767px) {
            padding-bottom: 0;
            margin-bottom: 50px;
            width: 100%;
        }
    }
    
    &__end {
        position: relative;
        width: 46%;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__title {
        margin: 0 0 rem(34);
        padding: 0;
    }
    
    &__email {
        margin-bottom: 46px;
        font-weight: 400;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.4;
        
        a {
            font-weight: 700;
            text-decoration: none;
        }
    }
    
    &__socials {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
        list-style: none;
        
        .menu-item {
            margin-right: rem(29);
            
            &:last-child {
                margin-right: 0;
            }
            
            a {
                color: $coregreen;
                
                &:hover {
                    color: $salmon;
                }
            }
        }
    }
    
    &__external-form {
        position: absolute;
        bottom: 0;
        @media (max-width: 767px) {
            position: static;
        }
    }
}
