.gs-share {
  display: flex;
  align-items: center;

  & > span {
    margin-right: rem(23);
    font-weight: 600;
    font-size: math-clamp(14, 16);
    line-height: 1;
    @media (max-width: 767px) {
      display: none;
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
  
    .menu-item {
      &:nth-child(n+2) {
        margin-left: rem(20);
      }
  
      a {
        @media (hover: hover) {
          &:hover {
            path {
              fill: $salmon;
            }
          }
        }
      }
  
      path {
        @extend %transition;
      }
    }
  }

  &_green {
    color: $coregreen;

    path {
      fill: $coregreen;
    }
  }
}