.gs-embeded-walkthrough-forward {
  position: relative;
  padding: rem(29) 0;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }

  &__left {
    width: 27%;
    background-color: $green;
    @media (max-width: 767px) {
      order: 1;
      width: 100%;
      height: calc(100% - 529px);
    }
  }

  &__right {
    flex: 1 1 auto;
    background-color: $coregreen;
    @media (max-width: 767px) {
      width: 100%;
      height: 529px;
    }
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    color: $white;
  }

  &__start {
    width: 50%;
    @media (max-width: 767px) {
      order: 1;
      margin-top: -3px;
      width: 100%;
    }
  }

  &__end {
    padding-left: rem(40);
    width: 50%;
    @media (max-width: 767px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-left: 0;
      width: 100%;
    }
  }

  &__demo {
    display: block;
    margin: 0 auto;
    width: math-clamp(235, 291);
  }

  &__title {
    margin-bottom: math-clamp(45, 54);
    @media (max-width: 767px) {
      text-align: center;
    }
  }

  &__downloads {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    a {
      margin: 0 rem(20) rem(20) 0;
    }

    svg {
      width: rem(130);
      height: auto;
    }
  }
}