.gs-questions-section {
    padding-top: math-clamp(70, 90);
    padding-bottom: math-clamp(60, 170);
    position: relative;
    
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $lightgrey;
        border-radius: 0 0 800px 0;
    }
    
    > .gs-container {
        position: relative;
        z-index: 5;
    }
    
    &__header {
        border-bottom: 2px solid $grey;
        padding-bottom: math-clamp(30, 50);
        margin-bottom: math-clamp(40, 85);
    }
    
    &__title {
        margin-bottom: 20px;
    }
    
    &__description {
        font-size: 16px;
        line-height: 26px;
    }
}
