.gs-content-slider {
    position: relative;
    padding-top: math-clamp(40, 120);
    
    @media (max-width: 991px) {
        padding-bottom: 50px;
    }
    
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        bottom: 0;
        background-color: $coregreen;
        z-index: 1;
    
        @media (min-width: 992px) {
            bottom: math-clamp(30, 100);
        }
    }
    
    &__container {
        width: calc(100% - 60px);
        max-width: 1320px;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        z-index: 5;
        display: flex;
        flex-wrap: wrap;
        gap: math-clamp(30, 115);
    }
    
    &__image {
        border-radius: 4px 150px 4px 4px;
        overflow: hidden;
        flex: 0 0 100%;
        max-width: 100%;
        height: math-clamp(300, 550);
    
        @media (min-width: 992px) {
            flex: 0 0 math-clamp(300, 550);
            max-width: math-clamp(300, 550);
        }
    
        @media (max-width: 991px) {
            margin-left: auto;
            margin-right: auto;
        }
    
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    
    &__slider {
        flex: 0 0 100%;
        max-width: 100%;
        
        @media (min-width: 992px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 440px;
            padding-top: 90px;
        }
    }
    
    
    &__item {
        color: $white;
        
        &-company-logo {
            max-width: 160px;
            margin-bottom: math-clamp(15, 35);
        }
        
        &-text {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            max-width: 436px;
            margin-bottom: math-clamp(15, 30);
        }
        
        &-info {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: math-clamp(20, 30);
            
            &-txt {
                flex-basis: 0;
                flex-grow: 1;
                max-width: 100%;
                font-weight: 350;
                font-size: 14px;
                line-height: 22px;
            }
        }
        
        &-small-image {
            flex: 0 0 100px;
            max-width: 100px;
            height: 100px;
            border-radius: 50%;
            border: 4px solid $highlighter;
            overflow: hidden;
    
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    
    &__nav {
        position: relative;
        z-index: 55;
        display: flex;
        justify-content: space-between;
    
        @media (min-width: 992px) {
            position: absolute;
            top: 30px;
            left: math-clamp(400, 665);
            right: 0;
        }
        @media (max-width: 991px) {
            align-items: center;
            margin-top: 40px;
        }
    }
    
    &__navigation {
        display: flex;
        gap: 20px;
        
        svg {
            display: block;
        }
    }
    
    &__pagination {
        display: flex;
        gap: 5px;
        
        .swiper-pagination-bullet {
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #4de6c5;
            transition: all .4s;
            
            &-active {
                background-color: $white;
            }
        }
    }
}

