.gs-wipe-tabs {
    $wt: &;
    
    padding: math-clamp(77, 117) 0 math-clamp(63, 110);
    background-color: $lightgrey;
    @media (max-width: 767px) {
        padding-top: 0;
    }
    
    &__header {
        padding-bottom: math-clamp(40, 115);
    
        @media (max-width: 767px) {
            background-color: $white;
        }
        
        .gs-btn {
            margin-top: math-clamp(30, 50);
        }
    }
    
    &__title {
        margin: 0 auto math-clamp(19, 33);
        max-width: math-clamp(926, 926);
        text-align: center;
    }
    
    &__image-text {
        background-color: $white;
        
        &-decor {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            padding-bottom: 50%;
            overflow: hidden;
    
            @media (min-width: 992px) {
                width: 50%;
                padding-bottom: 25%;
            }
            
            &:before {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 200%;
                border-radius: 50%;
                background-color: $coregreen;
            }
        }
        
        &-container {
            max-width: 1440px;
            margin-left: auto;
            margin-right: auto;
            position: relative;
        }
        
        &-row {
            display: flex;
            flex-wrap: wrap;
            position: relative;
            z-index: 5;
            align-items: center;
        }
        
        &-col {
            flex: 0 0 100%;
            max-width: 100%;
            
            @media (min-width: 992px) {
                flex: 0 0 50%;
                max-width: 50%;
            }
    
            &-content {
                padding: 0 math-clamp(30, 60) 40px math-clamp(30, 60);
    
                @media (min-width: 992px) {
                    padding: 0 math-clamp(30, 60);
                }
            }
        }
        
        &-image {
            padding-top: 40px;
            max-width: 400px;
            margin-left: auto;
            margin-right: auto;
        }
        
        &-content {
            @media (min-width: 992px) {
                max-width: 395px;
            }
    
            @media (max-width: 991px) {
                text-align: center;
            }
            
            h3 {
                margin-bottom: 10px;
            }
            
            .gs-btn {
                margin-top: 25px;
            }
        }
    }
    
    &__list-container {
        padding-top: 40px;
        background-color: $lightgrey;
        position: relative;
        
    
        @media (min-width: 992px) {
            margin-top: -128px;
            padding-top: 190px;
        }
    }
    
    &__subtitle {
        margin: 0 auto;
        max-width: rem(900);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.3;
        text-align: center;
    }
    
    &__main {
        @media (max-width: 767px) {
            margin-top: -20px;
        }
    }
    
    &__list {
        margin-top: -35px;
    }
    
    &__item {
        display: none;
        flex-wrap: wrap;
        justify-content: space-between;
        
        /*&:nth-child(3n+2) {
            #{$wt} {
                &__underlay {
                    background-color: $salmon;
                }
            }
        }
        
        &:nth-child(3n) {
            #{$wt} {
                &__underlay {
                    background-color: $green;
                }
            }
        }*/
    }
    
    &__start {
        padding-top: rem(70);
        display: flex;
        align-items: center;
        width: 36%;
        @media (max-width: 991px) {
            order: 1;
            padding-top: 0;
            width: 100%;
        }
    }
    
    &__end {
        width: rem(550);
        max-width: 100%;
        @media (max-width: 991px) {
            margin-bottom: 30px;
        }
    }
    
    &__name {
        margin: 0 0 rem(10);
        padding: 0;
    }
    
    &__text {
        margin-bottom: math-clamp(25, 45);
        font-weight: 400;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.6;
        
        p {
            margin-bottom: rem(30);
            
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    
    &__pic {
        position: relative;
    }
    
    &__poster {
        position: relative;
        width: rem(550);
        max-width: 100%;
        overflow: hidden;
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }

        &.js-wipe-rounded-poster {
            border-radius: math-clamp(2, 4) math-clamp(85, 150) math-clamp(2, 4) math-clamp(2, 4);
        }
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__underlay {
        position: absolute;
        bottom: 0;
        left: rem(-45);
        right: rem(-45);
        transform: translateY(#{rem(45)});
        width: calc(100% + #{rem(90)});
        height: rem(305);
        background-color: $highlighter;
        overflow: hidden;
        @media (max-width: 767px) {
            left: -17px;
            right: -17px;
            transform: translateY(17px);
            width: calc(100% + 34px);
            height: 160px;
        }
    }
    
    &__curtain {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;
        background-color: $highlighter;
        
        &_highlight {
            background-color: $highlighter;
        }
        
        &_salmon {
            background-color: $salmon;
        }
        
        &_green {
            background-color: $green;
        }
    }
    
    .gs-filter__list {
        min-width: 660px;
        justify-content: space-between;
    }
}
