@import "../variables";

.gs-title-section {
    $ts: &;
    
    &_show-border {
        #{$ts} {
            &__container {
                padding: math-clamp(50, 100) 0;
                border-top: 2px solid $grey;
                border-bottom: 2px solid $grey;
            }
        }
    }
    
    &__container {
        width: calc(100% - 72px);
        max-width: 1100px;
        margin-left: auto;
        margin-right: auto;
    }
    
    &__text {
        max-width: 920px;
        text-align: center;
        margin: 0 auto;
        padding: 0;
        
        span {
            background-image: linear-gradient(#d6fcb2, #d6fcb2);
            background-position: 0 calc(100% - 5px);
            background-repeat: no-repeat;
            background-size: 100% 10px;
            transition: background-size .3s;
        }
    }
}
