.gs-hero-1 {
    $h1: &;
    
    padding-bottom: 19px;
    position: relative;
    min-height: math-clamp(441, 775);
    color: $white;
    background-color: $lightgrey;
    visibility: hidden;
    
    @media (min-width: 1200px) {
        padding-bottom: 59px;
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: calc(100% - 19px);
        display: flex;
    
        @media (min-width: 1200px) {
            height: calc(100% - 59px);
        }
        @media (max-width: 575px) {
            display: none;
        }
        
        &-left,
        &-right {
            width: 50%;
        }
        
        &-left {
            background-color: $coregreen;
        }
        
        &-right {
            background-color: $lightgrey;
        }
    }
    
    &__content {
        position: relative;
        z-index: 1;
        min-height: inherit;
        height: 100%;
    }
    
    &__container {
        min-height: inherit;
        height: 100%;
    }
    
    &__wr {
        display: flex;
        flex-wrap: wrap;
        min-height: inherit;
        height: 100%;
    }
    
    &__start {
        display: flex;
        align-items: center;
        padding-right: rem(154);
        width: 50%;
        @media (max-width: 575px) {
            margin: 0 -36px;
            padding: 63px 36px 64px;
            width: calc(100% + 72px);
            background-color: $coregreen;
        }
    }
    
    &__end {
        position: relative;
        display: flex;
        align-items: center;
        width: 50%;
        @media (max-width: 575px) {
            margin: 0 -35px;
            padding: 44px 35px;
            width: calc(100% + 70px);
            background-color: $lightgrey;
        }
        
        #{$h1} {
            &__poster {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: none;
                @media (max-width: 575px) {
                    display: block;
                }
            }
        }
    }
    
    &__poster {
        width: 100%;
        height: 100%;
        border-radius: 0 0 rem(500) 0;
        overflow: hidden;
    }
    
    &__media {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__title {
        margin-bottom: math-clamp(14, 25);
        
        p {
            margin-bottom: 0;
        }
    }
    
    &__text {
        margin-bottom: math-clamp(28, 38);
        font-size: math-clamp(14, 24);
        line-height: 1.5;
    }
    
    &__links {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        
        & > * {
            margin-bottom: rem(26);
            margin-right: rem(26);
            
            &:last-child {
                margin-right: 0;
            }
        }
    }
    
    &__underline-link {
        font-weight: 600;
    }
    
    &__stats {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        width: 100%;
        @media (max-width: 991px) {
            flex-direction: column;
            justify-content: center;
        }
        
        &-start {
            margin-left: rem(-88);
            display: flex;
            flex-direction: column;
            
            & > *:nth-child(n+2) {
                margin-top: rem(17);
            }
        }
        
        &-end {
            @media (max-width: 991px) {
                order: -1;
                margin-bottom: 19px;
            }
        }
    }
    
    &__label {
        display: flex;
        align-items: center;
        padding: rem(10) rem(14);
        width: math-clamp(232, 250);
        background-color: $white;
        box-shadow: 2px 2px 11px rgba($black2, 0.15);
        border-radius: 8px;
        @media (max-width: 991px) {
            display: none;
            
            &:first-child {
                display: flex;
            }
        }
        
        & > *:nth-child(n+2) {
            margin-left: rem(17);
        }
        
        &-text {
            font-weight: 400;
            font-size: math-clamp(11, 12);
            color: $black;
            line-height: 1.33;
        }
        
        &-link {
            position: relative;
            padding-right: rem(10);
            font-weight: 700;
            font-size: math-clamp(9, 10);
            color: $coregreen;
            line-height: 1.2;
            
            &::after {
                content: '';
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%) rotate(-45deg);
                width: rem(5);
                height: rem(5);
                border: 1px solid $green;
                border-top: 0;
                border-left: 0;
            }
        }
    }
    
    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: math-clamp(48, 52);
        height: math-clamp(48, 52);
        background-color: $highlighter;
        border-radius: 50%;
    }
    
    &__chart {
        background: $white;
        box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.15);
        border-radius: math-clamp(7, 10);
        padding: math-clamp(15, 20) math-clamp(34, 43) math-clamp(17, 23);
        width: math-clamp(152, 206);
        
        &-price {
            margin-bottom: math-clamp(16, 26);
            font-weight: 700;
            font-size: math-clamp(17, 23);
            color: $coregreen;
            line-height: 1.2;
            text-align: center;
        }
        
        &-bar {
            display: flex;
            justify-content: space-between;
            margin-bottom: math-clamp(12, 16);
            width: 100%;
            height: math-clamp(106, 144);
        }
        
        &-item {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            width: math-clamp(36, 49);
            height: 100%;
            
            & > div {
                height: 100%;
                
                &:first-child {
                    border-radius: rem(5) rem(5) 0 0;
                    overflow: hidden;
                }
            }
            
            &:first-child {
                & > div {
                    background-color: $coregreen;
                }
            }
            
            &:last-child {
                & > div {
                    &:first-child {
                        height: math-clamp(38, 52);
                        background-color: $green;
                    }
                    
                    &:last-child {
                        height: math-clamp(25, 34);
                        background-color: $highlighter;
                    }
                }
            }
        }
        
        &-stats {
            display: flex;
            flex-direction: column;
            margin: 0;
            padding: 0;
            list-style: none;
            
            li {
                display: flex;
                align-items: center;
                font-family: $font-family-inter;
                font-weight: 500;
                font-size: math-clamp(8, 10);
                color: $black;
                
                & > span {
                    margin-left: rem(7);
                }
                
                &::before {
                    content: '';
                    width: math-clamp(11, 15);
                    height: math-clamp(11, 15);
                    border-radius: 50%;
                }
                
                &:first-child {
                    &::before {
                        background-color: $coregreen;
                    }
                }
                
                &:nth-child(n+2) {
                    margin-top: rem(10);
                    
                    &::before {
                        background-color: $green;
                    }
                }
                
                &:last-child {
                    &::before {
                        background-color: $highlighter;
                    }
                }
            }
        }
    }
}
