.gs-people {
  $ppl: &;

  position: relative;
  padding: math-clamp(62, 117) 0 math-clamp(107, 252);
  background-color: $white;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $lightgrey;
    border-radius: 0 0 0 rem(2000);
  }

  &__container {
    position: relative;
  }

  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    @media (max-width: 767px) {
      justify-content: flex-start;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: math-clamp(35, 75);
    @media (max-width: 991px) {
      flex-wrap: wrap;
    }
  }

  &__title {
    @media (max-width: 767px) {
      margin-bottom: 35px;
    }
  }

  &__textual {
    max-width: 56%;
    @media (max-width: 991px) {
      max-width: 100%;
    } 
  }

  &__text {
    font-weight: 400;
    font-size: rem(24);
    color: $black;
    line-height: 1.5;
  }

  &__bot {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__start {
    width: 17%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__end {
    width: 81%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__list {
    display: none;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%;

    &_active {
      display: flex;
    }
  }

  &__item {
    margin-left: 1%;
    margin-right: 1%;
    margin-bottom: rem(52);
    width: 31.333%;
    @media (min-width: 768px) {
      &:nth-last-child(-n+3) {
        margin-bottom: 0;
      }
    }
    @media (max-width: 767px) {
      width: 48%;
    }
    @media (max-width: 575px) {
      width: 100%;
    }

    @media (hover: hover) {
      &:hover {
        color: $coregreen;
  
        #{$ppl} {
          &__name {
            color: $coregreen;
          }

          &__socials {
            path {
              fill: $coregreen;
              fill-opacity: 1;
            }
          }
        }
      }
    }
  }

  &__poster {
    position: relative;
    margin-bottom: rem(19);
    width: 100%;
    border-radius: rem(4);
    overflow: hidden;

    &::after {
      content: '';
      display: block;
      padding-bottom: 100%;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__info {
    margin-bottom: math-clamp(7, 26)
  }

  &__name {
    @extend %transition;
    margin-bottom: math-clamp(10, 10);
    font-weight: 700;
    font-size: math-clamp(18, 18);
    color: $black;
    line-height: 1.3;
  }

  &__position {
    font-weight: 400;
    font-size: math-clamp(14, 14);
    color: $black;
    line-height: 1.6;
  }

  &__socials {
    display: flex;
    flex-wrap: wrap;

    &-item {
      margin-right: math-clamp(10, 15);

      &:last-child {
        margin-right: 0;
      }

      svg {
        width: rem(17);
        height: auto;

        path {
          @extend %transition;
        }
      }
    }
  }

  &__sidebar {
    position: relative;
    height: 100%;
  }

  &__category {
    display: flex;
    flex-direction: column;
    @media (max-width: 767px) {
      position: static;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    &-item {
      @media (max-width: 767px) {
        margin-right: 43px;
        margin-bottom: 40px;

        &:last-child {
          margin-right: 0;
        }
      }

      &:nth-child(n+2) {
        margin-top: rem(21);
        @media (max-width: 767px) {
          margin-top: 0;
        }
      }
    }
  }
}