.gs-hero-2 {
    position: relative;
    margin: rem(25) 0 rem(70);
    visibility: hidden;
    @media (max-width: 767px) {
        margin-bottom: 44px;
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        overflow: hidden;
        @media (max-width: 767px) {
            align-items: flex-end;
        }
    }
    
    &__container {
        max-width: rem(1292);
    }
    
    &__left {
        width: 50%;
        height: 100%;
        @media (max-width: 767px) {
            width: 100%;
            height: 55%;
        }
    }
    
    &__circle {
        position: relative;
        
        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: -100%;
            width: 200%;
            padding-bottom: 200%;
            background-color: $green;
            border-radius: 50%;
        }
    }
    
    &__content {
        position: relative;
        z-index: 1;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        padding: math-clamp(30, 90) 0 math-clamp(42, 81);
    }
    
    &__start {
        position: relative;
        padding-top: rem(30);
        padding-right: math-clamp(65, 98);;
        width: 50%;
        @media (max-width: 767px) {
            order: 1;
            padding-right: 17px;
            width: 100%;
        }
    }
    
    &__end {
        padding-left: rem(38);
        width: 50%;
        @media (max-width: 767px) {
            padding-left: 0;
            width: 100%;
        }
    }
    
    &__poster {
        position: relative;
        width: math-clamp(315, 550);
        max-width: 100%;
        
        &:after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
        
        &-wr {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: rem(4);
            overflow: hidden;
        }
    }
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__calculator {
        position: absolute;
        top: calc(100% - #{math-clamp(30, 59)});
        left: 50%;
        transform: translateX(-50%);
        width: math-clamp(216, 313);
        @media (max-width: 767px) {
            top: 100%;
            left: -8px;
            transform: translate(0, -50%);
        }
        @media (max-width: 374px) {
            top: calc(100% - 30px);
            transform: translate(0);
        }
        
        img {
            max-width: unset;
            width: 100%;
        }
    }
    
    &__chart {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(50%, -50%);
        width: math-clamp(150, 216);
        
        @media (max-width: 767px) {
            top: -61px;
            right: -17px;
            transform: translate(0);
        }
        @media (max-width: 374px) {
            top: -30px;
        }
    }
    
    &__title {
        margin-bottom: math-clamp(22, 35);
    }
    
    &__text {
        margin-bottom: math-clamp(22, 35);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.5;
    }
}
