.gs-image-content {
    $ic: &;
    
    position: relative;
    padding: math-clamp(96, 117) 0 79px;
    background-color: $lightgrey;
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    
    &__left {
        display: none;
        width: 50%;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__right {
        display: none;
        width: 50%;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__circle {
        width: 100%;
        transform: translateY(-50%);
        border-radius: 0 0 50% 50%;
        background-color: $coregreen;
        @media (max-width: 767px) {
            transform: translate(-50%, -50%);
        }
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
    }
    
    &__circle-big {
        width: 100%;
        height: 1440px;
        transform: translateX(0);
        overflow: hidden;
        
        @media (max-width: 575px) {
            height: 929px;
        }
        
        &:before {
            content: '';
            position: absolute;
            background-color: $white;
            border-radius: 50%;
            width: 1440px;
            height: 1440px;
    
            @media (max-width: 575px) {
                width: 929px;
                height: 929px;
            }
        }
    }
    
    &__content {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        z-index: 1;
    }
    
    &__start {
        width: 50%;
        @media (max-width: 575px) {
            margin-bottom: 61px;
            width: 100%;
        }
    }
    
    &__end {
        display: flex;
        align-items: center;
        padding-left: rem(61);
        width: 50%;
        @media (max-width: 767px) {
            padding-left: 30px;
        }
        @media (max-width: 575px) {
            padding-left: 0;
            width: 100%;
        }
    }
    
    &__poster {
        position: relative;
        max-width: 100%;
        width: math-clamp(316, 550);
        overflow: hidden;
        @media (max-width: 575px) {
            width: 100%;
        }
        
        &:after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__textual {
        max-width: rem(393);
    }
    
    &__title {
        margin: 0 0 rem(10);
        padding: 0;
        @media (max-width: 767px) {
            margin: 0 0 19px;
        }
    }
    
    &__text {
        margin-bottom: rem(28);
        font-weight: 400;
        font-size: rem(16);
        color: $black;
        line-height: 1.6;
        @media (max-width: 767px) {
            margin-bottom: 43px;
        }
    }
    
    &_reverse {
        #{$ic} {
            &__start {
                display: flex;
                justify-content: flex-end;
            }
            
            &__content {
                flex-direction: row-reverse;
            }
            
            /*&__poster {
                border-radius: math-clamp(2, 4) math-clamp(2, 4) math-clamp(2, 4) math-clamp(86, 150);
            }*/
        }
    }
    
    &_with-green-circle {
        padding: math-clamp(57, 177) 0 math-clamp(70, 126);
        overflow: hidden;
        background-color: $white;
        
        #{$ic} {
            &__left {
                display: block;
            }
        }
    }

    &_with-light-green-circle {
        padding: math-clamp(57, 177) 0 math-clamp(70, 126);
        overflow: hidden;
        background-color: $white;
        
        #{$ic} {
            &__left {
                display: block;
            }

            &__circle {
                background-color: $green;
            }
        }
    }
    
    &_with-circle-big {
        #{$ic} {
            &__bg {
                justify-content: flex-end;
            }
            
            &__right {
                display: block;
            }
        }
    }
    
    &_with-right-circle {
        padding-top: 80px;
        background-color: transparent;
        
        > .gs-container {
            position: relative;
            z-index: 5;
        }
        
        &:before {
            content: '';
            position: absolute;
            top: math-clamp(145, 245);
            left: 0;
            bottom: 0;
            width: 100%;
            background-color: $lightgrey;
            border-radius: 0 500px 0 0;
        }
    
        #{$ic} {
            &__end {
                padding-top: math-clamp(40, 100);
            }
        }
    }
    
    &_with-bottom-right-circle {
        background-color: transparent;
    
        > .gs-container {
            position: relative;
            z-index: 5;
        }
    
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $lightgrey;
            border-radius: 0 0 500px 0;
        }
    }
    
    &_radius-image-top-left {
        #{$ic} {
            &__poster {
                border-radius: math-clamp(86, 150) math-clamp(2, 4) math-clamp(2, 4) math-clamp(2, 4);
            }
        }
    }
    &_radius-image-top-right {
        #{$ic} {
            &__poster {
                border-radius: math-clamp(2, 4) math-clamp(86, 150) math-clamp(2, 4) math-clamp(2, 4);
            }
        }
    }
    &_radius-image-bottom-right {
        #{$ic} {
            &__poster {
                border-radius: math-clamp(2, 4) math-clamp(2, 4) math-clamp(86, 150) math-clamp(2, 4);
            }
        }
    }
    &_radius-image-bottom-left {
        #{$ic} {
            &__poster {
                border-radius: math-clamp(2, 4) math-clamp(2, 4) math-clamp(2, 4) math-clamp(86, 150);
            }
        }
    }
    
    & + & {
        padding-top: 0;
    }
}
