.gs-contact-block-1 {
    padding-top: math-clamp(100, 200);
    visibility: hidden;
    
    @media (max-width: 767px) {
        padding-top: 42px;
    }
    
    &__top {
        position: relative;
        width: 100%;
        
        /*&::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $lightgrey;
            border-radius: 0 math-clamp(250, 700) 0 0;
        }*/
    }
    
    &__content {
        position: relative;
        z-index: 1;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        //align-items: center;
    }
    
    &__start {
        width: 46%;
        padding-bottom: math-clamp(50, 120);
    
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__end {
        position: relative;
        width: 46%;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__bot {
        position: relative;
        width: 100%;
        background-color: $green;
        @media (max-width: 767px) {
            margin-top: -88px;
            padding-bottom: 53px;
        }
    }
    
    &__poster {
        position: relative;
        width: 100%;
        height: math-clamp(350, 700);
        border-radius: rem(400) 0 0 rem(400);
        overflow: hidden;
        @media (max-width: 767px) {
            padding-bottom: 53px;
        }
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 57%;
            @media (max-width: 767px) {
                display: none;
            }
        }
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__title {
        margin: 0 0 math-clamp(24, 24);
    }
    
    &__subtitle {
        font-weight: 400;
        font-size: math-clamp(24, 24);
    }
    
    &__external-form {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        padding-bottom: math-clamp(42, 72);
        
        @media (max-width: 767px) {
            position: static;
            transform: translate(0);
        }
    }
}
