.gs-embeded-walkthrough-with-tabs {
    $ew: &;
    
    &__header {
        margin-bottom: rem(45);
        text-align: center;
        @media (max-width: 767px) {
            margin-bottom: 57px;
        }
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: 100%;
    }
    
    &__left {
        width: 50%;
        overflow: hidden;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__circle {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: $lightgrey;
        border-radius: 0 rem(700) 0 0;
        @media (max-width: 767px) {
            top: 135px;
            border-radius: 700px 0 0 0;
        }
    }
    
    &__title {
        margin: 0 0 math-clamp(25, 34);
    }
    
    &__subtitle {
        margin-bottom: rem(45);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.3;
        
        strong {
            font-weight: 700;
            color: $coregreen;
        }
    }
    
    &__main {
        position: relative;
        z-index: 1;
    }
    
    &__wr {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    
    &__content {
        position: relative;
        padding: math-clamp(65, 100) 0 math-clamp(95, 100);
        @media (max-width: 767px) {
            margin-top: 65px;
            padding-top: 0;
        }
    }
    
    &__start {
        width: 50%;
        @media (max-width: 767px) {
            margin-bottom: 56px;
            padding-bottom: 80px;
            width: 100%;
        }
    }
    
    &__end {
        width: 50%;
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__demo {
        display: block;
        margin: 0 auto;
        width: math-clamp(235, 291);
        border-radius: rem(50);
    }
    
    &__textual {
        display: none;
        margin: 0 auto;
        max-width: rem(500);
        
        &.active {
            display: block;
        }
    }
    
    &__name {
        margin: 0 0 rem(9);
        @media (max-width: 767px) {
            margin-bottom: 20px;
        }
    }
    
    &__text {
        font-weight: 400;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.6;
    }
}
