.gs-short-hero {
  padding: math-clamp(16, 80) 0 math-clamp(25, 45);
  visibility: hidden;

  &__content {
    display: flex;
    flex-wrap: wrap;
  }

  &__start {
    width: 52%;
    @media (max-width: 767px) {
      margin-bottom: 18px;
      width: 100%;
    }
  }

  &__end {
    width: 48%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__textual {
    padding-left: rem(45);
    max-width: rem(484);
    @media (max-width: 767px) {
      padding-left: 0;
    }
  }

  &__text {
    font-weight: 400;
    font-size: math-clamp(20, 24);
    color: $coregreen;
    line-height: 1.5;
  }

  &__more {
    margin-top: math-clamp(20, 30);
  }

  &__underline-link {
    @extend %transition;
    color: $black;

    &:hover {
      color: $coregreen;
    }
  }
}