.gs-video-block {
    $vb: &;
    
    margin-bottom: math-clamp(50, 89);
    visibility: hidden;
    cursor: pointer;
    
    &_white {
        background-color: $white;
    }
    &_tan-grey {
        background-color: $lightgrey;
    }
    &_top-bottom-indent {
        padding-top: 80px;
        padding-bottom: 80px;
        @media (max-width: 767px) {
            padding-top: 40px;
            padding-bottom: 40px;
        }
    }
    
    &__poster {
        position: relative;
        width: 100%;
        border-radius: rem(4);
        overflow: hidden;
        
        &:hover {
            #{$vb} {
                &__play {
                    circle {
                        fill: $salmon;
                    }
                }
            }
        }
    }
    
    &__link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    
    &__img {
        @extend %transition;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__play {
        @extend %transition;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: math-clamp(50, 50);
        
        svg {
            width: 100%;
            height: auto;
            
            circle {
                @extend %transition;
            }
        }
    }
    
    &__video,
    iframe {
        display: block;
        width: 100%;
        height: auto;
    }
    
    iframe {
        height: math-clamp(152, 575);
    }
    
    &_active {
        #{$vb} {
            &__img {
                opacity: 0;
            }
            
            &__play {
                opacity: 0;
            }
        }
    }
}
