.gs-image-block {
    position: relative;
    
    &:before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50%;
        background-color: $coregreen;
    }
    
    &__container {
        width: 100%;
        height: math-clamp(320, 700);
        overflow: hidden;
        border-radius: 0 500px 500px 0;
        position: relative;
        z-index: 5;
        
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

