.gs-resources-list-with-filter {
    position: relative;
    padding: math-clamp(55, 126) 0 math-clamp(168, 178);
    
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $lightgrey;
        border-radius: 0 0 math-clamp(217, 800) 0;
    }
    
    &__content {
        position: relative;
        z-index: 1;
    }
    
    &__top {
        margin-bottom: math-clamp(50, 69);
    }
    
    &__mid {
        margin-bottom: math-clamp(36, 58);
    }
    
    &__bod {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    &__pagination {
        .nav-links {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
        }
        
        .page-numbers {
            margin-right: rem(15);
            font-weight: 400;
            font-size: rem(16);
            color: $black;
            line-height: 1;
            text-decoration: none;
            background-color: transparent;
            border: none;
            
            &.current {
                font-weight: 700;
                color: $coregreen;
            }
            
            svg {
                width: rem(13);
                height: auto;
            }
        }
    }
    
    .js-resources-filter-form-paginate {
        display: none;
    }
}
