.gs-checkmark-list {
  padding: math-clamp(35, 45) 0 math-clamp(100, 100);

  &__header {
    margin-bottom: math-clamp(57, 85);
  }

  &__title {
    text-align: center;
  }

  &__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    margin-bottom: rem(35);
    width: 40%;
    @media (min-width: 768px) {
      &:nth-last-child(-n+2) {
        margin-top: 30px;
        margin-bottom: 0;
      }
    }
    @media (max-width: 767px) {
      margin-bottom: 42px;
      width: 100%;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__icon {
    margin-right: rem(19);

    svg {
      width: rem(60);
      height: auto;
    }
  }

  &__name {
    margin-bottom: rem(10);
    font-weight: 700;
    font-size: rem(16);
    color: $black;
    line-height: 1.5;
  }

  &__text {
    font-weight: 400;
    font-size: rem(16);
    color: $black;
    line-height: 1.6;
  }

  &__description {
    padding-top: math-clamp(32, 39);
    border-top: 1px solid $grey;
    font-weight: 700;
    font-size: rem(16);
    color: $black;
    line-height: 1.5;
    @media (max-width: 767px) {
      margin-bottom: 24px;
    }

    &-text {
      margin-bottom: rem(15);
      @media (max-width: 767px) {
        margin-bottom: 25px;
      }
    }
  }
}