.gs-hero-3 {
  position: relative;
  margin: rem(20) 0 rem(48);
  visibility: hidden;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    @media (max-width: 767px) {
      align-items: flex-end;
    }
  }

  &__left {
    position: relative;
    width: 50%;
    height: 100%;
    overflow: hidden;
    @media (max-width: 767px) {
      width: 100%;
      height: 50%;
    }
  }

  &__circle {
    position: relative;
    width: 100%;
    height: 100%;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $green;
      border-radius: 0 0 100% 0;
    }
  }

  &__container {
    max-width: rem(1221);
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: math-clamp(50, 100) 0;
  }

  &__start {
    display: flex;
    align-items: flex-end;
    width: 50%;
    @media (max-width: 767px) {
      order: 1;
      width: 100%;
    }

    &-right {
      margin-left: rem(28);
    }
  }

  &__chart {
    width: math-clamp(128, 184);

    svg {
      width: 100%;
      height: auto;
      transform: translateX(rem(-12));
    }
  }

  &__stats {
    width: math-clamp(153, 267);
    
    svg {
      transform: translateX(rem(-5));
      width: 100%;
      height: auto;
    }
  }

  &__phone {
    position: relative;

    &-img {
      width: math-clamp(155, 224);
    }

    &-ava {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-50%, -50%);
      width: math-clamp(75, 150);
      height: math-clamp(75, 150);
      border: math-clamp(4, 8) solid $highlighter;
      border-radius: 50%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  &__end {
    padding-left: rem(44);
    width: 50%;
    @media (max-width: 767px) {
      margin-bottom: 50px;
      padding-left: 0;
      width: 100%;
    }
  }

  &__title {
    margin: 0 0 math-clamp(23, 33);
  }

  &__text {
    margin-bottom: math-clamp(28, 38);
    font-weight: 400;
    font-size: math-clamp(20, 24);
    color: $black;
    line-height: 1.5;
  }

  &__more {
    margin-bottom: math-clamp(20, 30);
  }

  &__downloads {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    a {
      margin: 0 rem(20) rem(20) 0;
    }

    svg {
      width: rem(130);
      height: auto;
    }
  }
}