.gs-forward {
  $fwd: &;

  position: relative;
  padding-top: rem(90);
  background-color: $green;
  @media (max-width: 767px) {
    padding-top: 95px;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
  }

  &__left {
    width: 71%;
    height: calc(100% - #{rem(100)});
    background-color: $coregreen;
    @media (max-width: 767px) {
      width: 100%;
      height: 529px;
    }
  }

  &__right {
    width: 29%;
    height: calc(100% - rem(100));
    background-color: transparent;
    @media (max-width: 767px) {
      width: 100%;
      height: 200px;
      background-color: $green;
    }
  }

  &__bot {
    width: 100%;
    height: rem(100);
    background-color: $white;
    @media (max-width: 767px) {
      width: 100%;
      height: calc(100% - 729px);
    }

    &_black {
      background-color: $black;
    }
  }

  &__content {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__start {
    padding: rem(100) 0;
    width: 42%;
    @media (max-width: 767px) {
      margin-bottom: 95px;
      padding: 0;
      width: 100%;
    }
  }

  &__end {
    max-width: 55%;
    width: rem(650);
    @media (max-width: 767px) {
      max-width: 100%;
      width: 100%;
    }
  }

  &__textual {
    max-width: rem(447);
    padding-right: rem(30);
    @media (max-width: 767px) {
      padding-right: 30px;
    }
  }

  &__text {
    margin-bottom: rem(45);
    font-weight: 400;
    font-size: math-clamp(20, 24);
    color: $white;
    line-height: 1.6;
    @media (max-width: 767px) {
      margin-bottom: 37px;
    }

    p {
      margin-bottom: rem(20);

      &:last-child {
        margin-bottom: 0;
      }
    }

  }

  &__title {
    margin-bottom: rem(41);
    color: $white;
    @media (max-width: 767px) {
      margin-bottom: 48px;
    }
  }

  &__poster {
    position: relative;
    width: rem(650);
    max-width: 100%;
    border-radius: math-clamp(2, 4) math-clamp(73, 150) math-clamp(2, 4) math-clamp(2, 4);
    overflow: hidden;
    @media (max-width: 767px) {
      margin: 0 auto;
    }

    &::after {
      content: '';
      display: block;
      padding-bottom: 100%;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &_reversed {
    margin-bottom: rem(38);
    @media (max-width: 767px) {
      margin-bottom: rem(111);
      padding-top: 59px;
    }

    #{$fwd} {
      &__left {
        width: 100%;
        @media (max-width: 767px) {
          height: calc(100% - 90px);
        }
      }

      &__bot {
        @media (max-width: 767px) {
          height: 90px;
        }
      }

      &__right {
        display: none;
      }

      &__start {
        padding-top: 0;
        order: 1;
        @media (min-width: 992px) {
          width: 47%;
        }
        @media (max-width: 767px) {
          margin-bottom: 45px;
          order: 0;
        }
      }

      &__end {
        @media (min-width: 992px) {
          width: 47%;
        }
      }

      &__title {
        margin-bottom: rem(30);
      }

      &__textual {
        max-width: 100%;
      }

      &__text {
        font-size: math-clamp(14, 16);
        @media (max-width: 767px) {
          margin-bottom: 37px;
        }
      }

      &__poster {
        width: rem(550);
      }
    }
  }
}