.gs-content-text-blocks {
    width: calc(100% - 80px);
    max-width: 1180px;
    margin-left: auto;
    margin-right: auto;
    
    &__title {
        max-width: 800px;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        margin-bottom: math-clamp(20, 30);
    }
    
    &__text {
        max-width: 800px;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        font-weight: 400;
        font-size: math-clamp(20, 24);
        line-height: 150%;
        margin-bottom: math-clamp(40, 90);
    }
    
    &__row {
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
        
        @media (min-width: 992px) {
            gap: 40px;
        }
        @media (min-width: 1200px) {
            gap: 60px;
        }
    }
    
    &__item {
        $cit: &;
        
        position: relative;
        height: 250px;
        flex: 0 0 100%;
        max-width: 100%;
    
        @media (min-width: 768px) {
            flex: 0 0 calc(50% - 15px);
            max-width: calc(50% - 15px);
        }
        @media (min-width: 992px) {
            flex: 0 0 calc(33.33333% - 26.66666px);
            max-width: calc(33.33333% - 26.66666px);
        }
        @media (min-width: 1200px) {
            flex: 0 0 calc(25% - 45px);
            max-width: calc(25% - 45px);
        }
    
        &:before {
            content: '';
            position: absolute;
            top: 2px;
            left: 2px;
            width: calc(100% - 4px);
            height: calc(100% - 4px);
            background-color: $highlighter;
            transition: all .4s;
            z-index: 1;
        }
    
        @media (hover: hover) {
            &:hover {
                &:before {
                    top: 22px;
                    left: 22px;
                }
                
                #{$cit} {
                    &-icon:not(.gs-content-text-blocks__item-icon_fill) {
                        path {
                            stroke: $salmon;
                        }
                    }
                
                    &-icon_fill {
                        path {
                            fill: $salmon;
                        }
                    }
    
                    &-description {
                        height: 60px;
                    }
                }
            }
        }
        
        &:hover {
            &:before {
                top: 22px;
                left: 22px;
            }
        }
        
        &-wrap {
            position: relative;
            z-index: 5;
            padding: 20px 25px;
            background: $white;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
            border-radius: 4px;
        }
        
        &-icon {
            position: relative;
            display: flex;
            align-items: flex-start;
            margin-bottom: 15px;
            justify-content: center;
    
            svg {
                max-width: rem(50);
                height: auto;
        
                path {
                    @extend %transition;
                }
            }
        }
        
        &-title {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            margin-bottom: 10px;
        }
        
        &-description {
            transition: all .4s;
            text-align: center;
            font-weight: 400;
            font-size: 16px;
            line-height: 1.625;
            overflow: hidden;
    
            @media (min-width: 1024px) and (hover: hover) {
                height: 0;
            }
        }
    }
}
