.gs-three-icons-section {
    background-color: $lightgrey;
    padding-top: math-clamp(20, 50);
    padding-bottom: math-clamp(30, 70);
    
    &__header {
        padding-bottom: math-clamp(40, 50);
    }
    
    &__title {
        margin: 0 auto math-clamp(19, 33);
        max-width: math-clamp(926, 926);
        text-align: center;
    }
    
    &__subtitle {
        margin: 0 auto;
        max-width: rem(900);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.3;
        text-align: center;
    }
    
    &__container {
        width: calc(100% - 60px);
        max-width: 1120px;
        margin-left: auto;
        margin-right: auto;
        background: #FFFFFF;
        box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
        border-radius: 32px;
        padding: 40px 30px 45px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: math-clamp(40, 90);
        
        @media (max-width: 767px) {
            flex-direction: column;
            align-items: center;
        }
    }
    
    &__item {
        $it: &;
        
        flex: 0 0 188px;
        max-width: 188px;
        text-align: center;
        
        &:nth-child(1) {
            svg {
                circle {
                    stroke: $highlighter;
                }
            }
        }
        &:nth-child(2) {
            svg {
                circle {
                    stroke: $green;
                }
            }
        }
        &:nth-child(3) {
            svg {
                circle {
                    stroke: $coregreen;
                }
            }
        }
    
        &-line {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 100%;
            height: 2px;
            //width: 85%;
            width: 0;
            background-color: $grey;
        
            @media (max-width: 767px) {
                display: none;
            }
        }
        
        &-img {
            position: relative;
            width: 150px;
            height: 150px;
            margin-bottom: 17px;
            margin-left: auto;
            margin-right: auto;
            
            &-wrap {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 5;
                width: 68px;
                height: 54px;
                background-color: $white;
            }
            
            svg {
                display: block;
                width: 100%;
                height: 100%;
            }
            
            img {
                max-width: 100%;
                max-height: 100%;
                object-position: center;
            }
        }
        
        &-text {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: $coregreen;
        }
    }
}
