.gs-text-with-pic-on-ellipse {
  position: relative;
  padding: math-clamp(54, 114) 0 math-clamp(35, 54);
  background-color: $white;
  overflow: hidden;

  @media (max-width: 767px) {
    margin-bottom: 35px;
    padding-bottom: 0;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__circle {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50% / 100%;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: $lightgrey;
    @media (max-width: 767px) {
      border-radius: 0 0 2000px 0;
    }
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }

  &__start {
    width: 44%;
    @media (max-width: 767px) {
      margin-bottom: 55px;
      width: 100%;
    }
  }

  &__end {
    display: flex;
    justify-content: flex-end;
    width: 50%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__title {
    margin-bottom: math-clamp(17, 29);
  }

  &__text {
    margin-bottom: rem(30);
    @media (max-width: 767px) {
      margin-bottom: 44px;
    }
  }

  &__poster {
    position: relative;
    width: rem(550);
    border-radius: math-clamp(4, 4) math-clamp(75, 150) math-clamp(4, 4) math-clamp(4, 4);
    overflow: hidden;

    &::after {
      content: '';
      display: block;
      padding-bottom: 100%;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

}