.gs-accordeon {
  $acc: &;

  &__item {
    border-bottom: rem(2) solid $grey;

    &_active {
      #{$acc} {
        &__toggler {
          transform: rotate(-180deg);
        }
      }
    }
  }

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: rem(41) 0;
  }

  &__name {
    padding: 0;
    margin: 0;
  }

  &__toggler {
    @extend %transition;
    margin-left: rem(32);
    min-width: rem(32);
    cursor: pointer;
    transform-origin: center;

    svg {
      width: 100%;
      height: auto;
    }
  }

  &__body {
    @extend %transition;
    height: 0;
    overflow: hidden;
  }

  &__textual {
    margin-bottom: rem(41);
  }
}