.gs-form-subscribe {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-end;
    padding: rem(20) rem(20) rem(20) rem(30);
    background-color: $white;
    background: $white;
    box-shadow: rem(1) rem(1) rem(15) rgba($black2, 0.1);
    border-radius: rem(8);
    
    &__start {
        width: calc(100% - #{rem(184)});
        @media (max-width: 991px) {
            margin-bottom: 20px;
            width: 100%;
        }
    }
    
    &__end {
        display: flex;
        justify-content: flex-end;
        @media (max-width: 991px) {
            justify-content: center;
            width: 100%;
        }
    }
    
    &__label {
        display: flex;
        flex-direction: column;
        
        & > span {
            margin-bottom: rem(4);
            font-weight: 700;
            font-size: math-clamp(14, 16);
            color: $black;
            line-height: 1.1;
        }
    }
    
    &__input {
        padding: 0 0 rem(4);
        width: 100%;
        height: rem(36);
        border: 0;
        background-color: transparent;
        border-bottom: rem(2) solid $grey;
        
        &::placeholder {
            color: $grey3;
            opacity: 1;
        }
    }
}
