.gs-external-form {
    z-index: 1;
    padding: math-clamp(38, 60) math-clamp(26, 76) math-clamp(47, 47);
    width: 100%;
    background-color: $white;
    box-shadow: rem(1) rem(1) rem(15) rgba($black2, 0.1);
    border-radius: rem(32);
    overflow-x: hidden;
    overflow-y: visible;
    
    form {
        & > *:nth-child(n+2) {
            margin-top: rem(30);
        }
    }
    
    label {
        margin-bottom: rem(9);
        font-weight: 700;
        font-size: rem(16);
        color: $black;
        line-height: 1.9;
    }
    
    input[type=text],
    input[type=email],
    input[type=password] {
        @extend %transition;
        width: 100%;
        height: rem(30);
        border: 0;
        border-bottom: rem(2) solid $coregreen;
        font-weight: 400;
        font-size: rem(16);
        color: $black;
        
        &:focus {
            border-bottom-color: $green;
        }
        
        &::placeholder {
            color: $grey3;
            opacity: 1;
        }
    }
    
    textarea {
        @extend %transition;
        width: 100%;
        height: 100px;
        border: 0;
        border-bottom: rem(2) solid $coregreen;
        font-weight: 400;
        font-size: rem(16);
        color: $black;
        outline: none;
        resize: none;
        
        &:focus {
            border-bottom-color: $green;
        }
        
        &::placeholder {
            color: $grey3;
            opacity: 1;
        }
    }
    
    input[type=submit],
    button[type=submit] {
        @extend %transition;
        @extend %green-btn;
    }
    
    ul {
        list-style: none;
        margin-left: 0;
    }
    
    .inputs-list.multi-container {
        display: flex;
        
        & > * {
            margin-right: rem(20);
        }
    }
    
    .hs-form-radio-display {
        position: relative;
        display: flex;
        align-items: center;
        
        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            
            &:checked ~ span {
                &::before {
                    background-image: url('../img/radio-desctop-active.svg');
                    @media (max-width: 767px) {
                        background-image: url('../img/radio-mobile-active.svg');
                    }
                }
            }
        }
        
        & > span {
            margin-left: rem(26);
            @media (max-width: 767px) {
                margin-left: 31px;
            }
            
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                z-index: 1;
                display: block;
                width: rem(16);
                height: rem(16);
                background-image: url('../img/radio-desctop.svg');
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                @media (max-width: 767px) {
                    width: 21px;
                    height: 21px;
                    background-image: url('../img/radio-mobile.svg');
                }
            }
        }
    }
    
    .hs-error-msg {
        color: $salmon;
    }
    
    select {
        position: absolute !important;
        left: -9999px !important;
        height: 1px !important;
        width: 1px !important;
        margin: 0px !important;
        border-width: -108px;
        appearance: none;
    }
    
    .jcf-select {
        @extend %transition;
        width: 100%;
        height: rem(30);
        border: 0;
        border-bottom: rem(2) solid $coregreen;
        
        &-drop {
            top: calc(100% + 10px);
            box-shadow: rem(3) rem(3) rem(17) rgba($black2, 0.15);
            border-radius: rem(8);
            overflow: hidden;
        }
        
        &-text {
            margin-left: 0 !important;
            font-family: $font-family-lft-etica !important;
            font-weight: 400 !important;
            font-size: rem(16) !important;
            color: $black !important;
            cursor: pointer;
        }
        
        &-opener {
            @extend %transition;
            top: unset;
            bottom: rem(17);
            background-color: transparent !important;
            background-image: url('../img/accordeon-toggler.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            width: rem(32);
            height: rem(32);
            transform-origin: center;
        }
        
        &.jcf-drop-active {
            .jcf-select-opener {
                transform: rotate(-180deg);
            }
        }
        
        .jcf-list {
            font-family: $font-family-lft-etica !important;
            font-weight: 400 !important;
            font-size: rem(16) !important;
            color: $black !important;
            
            li {
                display: flex;
                align-items: center;
                padding: 0 rem(19);
                width: 100%;
                min-height: rem(46);
                background-color: $white;
                cursor: pointer;
                
                &:first-child {
                    display: none;
                }
                
                &:nth-child(even) {
                    background-color: $lightgrey;
                }
            }
        }
        
        &.jcf-focus, &.jcf-focus * {
            border-color: $green !important;
            border-width: rem(2) !important;
        }
        
        .jcf-select-drop .jcf-select-drop-content {
            border-color: transparent !important;
            border-width: 0 !important;
        }
        
        .jcf-option.jcf-hover {
            background-color: transparent !important;
            color: inherit !important;
        }
        
        .jcf-option {
            white-space: normal;
        }
    }
}
