.gs-calculator {
    position: relative;
    background-color: $white;
    overflow: hidden;
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-end;
        @media (max-width: 767px) {
            flex-direction: column;
        }
    }
    
    &__left,
    &__right {
        width: 50%;
        height: rem(496);
        @media (max-width: 767px) {
            width: 100%;
        }
    }
    
    &__left {
        background-color: $lightgrey;
        @media (max-width: 767px) {
            height: 780px;
        }
    }
    
    &__right {
        background-color: $green;
        @media (max-width: 767px) {
            height: calc(100% - 780px);
        }
    }
    
    &__content {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
    }
    
    &__start {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 50%;
        height: rem(496);
        @media (max-width: 991px) {
            padding-right: 30px;
        }
        @media (max-width: 767px) {
            padding-top: 80px 0 92px;
            width: 100%;
        }
    }
    
    &__end {
        padding-bottom: rem(55);
        display: flex;
        justify-content: flex-end;
        width: 50%;
        @media (max-width: 991px) {
            padding-bottom: 90px;
            padding-left: 30px;
        }
        @media (max-width: 767px) {
            padding-bottom: 194px;
            width: 100%;
        }
    }
    
    &__textual {
        position: relative;
        max-width: rem(424);
    }
    
    &__title {
        margin: 0 0 rem(11);
        padding: 0;
        font-weight: 600;
        @media (max-width: 767px) {
            margin-bottom: 19px;
        }
    }
    
    &__text {
        margin-bottom: math-clamp(19, 49);
        font-weight: 400;
        font-size: rem(16);
        color: $black;
        line-height: 1.6;
    }
    
    &__wr {
        position: relative;
        max-width: rem(449);
        @media (max-width: 767px) {
            display: flex;
            justify-content: center;
            max-width: unset;
            width: 100%;
        }
    }
    
    &__body {
        width: 100%;
        height: auto;
        @media (max-width: 767px) {
            width: sd(449);
        }
        @media (max-width: 575px) {
            width: 294px;
        }
    }
    
    &__avas {
        position: absolute;
        top: rem(54);
        right: calc(100% - #{math-clamp(75, 100)});
        display: flex;
        align-items: center;
        @media (max-width: 991px) {
            top: unset;
            right: unset;
            left: 50%;
            bottom: rem(-90);
            transform: translateX(-50%);
        }
        
        & > *:nth-child(n+2) {
            margin-left: math-clamp(14, 21);
        }
    }
    
    &__poster {
        width: rem(150);
        height: rem(150);
        border: 8px solid $highlighter;
        border-radius: 50%;
        overflow: hidden;
        
        &_last {
            transform: translateY(#{rem(-5)});
            border-color: $salmon;
        }
    }
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__plus {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 !important;
        width: rem(52);
        height: rem(52);
        background-color: $white;
        border-radius: 50%;
        font-weight: 700;
        font-size: rem(36);
        color: $coregreen;
        line-height: 1;
        
        span {
            display: block;
            transform: translateY(#{rem(-3)});
        }
    }
}
