.gs-post-list {
    $pl: &;
    
    display: flex;
    flex-wrap: wrap;
    // gap: math-clamp(36, 51) 0;
    margin-left: -2.5%;
    margin-right: -2.5%;
    
    & > * {
        margin-bottom: math-clamp(36, 51);
    }
    
    
    &__full {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-left: 2.5%;
        margin-right: 2.5%;
        width: 100%;
        
        &-start {
            width: 47%;
            @media (max-width: 767px) {
                margin-bottom: 36px;
                width: 100%;
            }
        }
        
        &-end {
            display: flex;
            align-items: center;
            width: 47%;
            @media (max-width: 767px) {
                width: 100%;
            }
        }
        
        &-poster {
            width: 100%;
            height: math-clamp(202, 350);
            border-radius: rem(4);
            background-color: $lightgrey;
            overflow: hidden;
        }
        
        &-img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        
        &-name {
            margin: 0 0 rem(14);
            padding: 0;
            @media (max-width: 767px) {
                margin-bottom: 17px;
            }
        }
        
        &-text {
            margin-bottom: math-clamp(28, 38);
            font-weight: 400;
            font-size: rem(16);
            color: $black;
            line-height: 2;
        }
    }
    
    &__card {
        position: relative;
        margin-left: 2.5%;
        margin-right: 2.5%;
        width: 28.333%;
        background: $white;
        box-shadow: rem(1) rem(1) rem(15) rgba($black2, 0.1);
        border-radius: rem(4);
        background-color: $white;
        @media (max-width: 991px) {
            width: 45%;
            min-height: 325px;
        }
        @media (max-width: 575px) {
            width: 100%;
        }
        
        &::before {
            @extend %transition;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $highlighter;
        }
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
        
        &:hover {
            &::before {
                transform: translate(#{math-clamp(17, 30)}, math-clamp(17, 30));
            }
            
            #{$pl} {
                &__textual {
                    //padding: rem(47) math-clamp(26, 32) rem(32);
                    height: 100%;
                }
                
                &__time {
                    opacity: 1;
                }
                
                &__description {
                    opacity: 1;
                }
            }
        }
    }
    
    &__poster {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 54%;
    }
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__textual {
        @extend %transition;
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        padding: math-clamp(12, 25) math-clamp(26, 32) math-clamp(12, 25) math-clamp(12, 25);
        width: 100%;
        height: 46%;
        background-color: $white;
        z-index: 1;
        overflow: hidden;
    }
    
    &__time {
        @extend %transition;
        opacity: 0;
        position: absolute;
        top: math-clamp(12, 14);
        right: math-clamp(17, 21);
        font-weight: 350;
        font-size: math-clamp(12, 14);
        color: $grey2;
        line-height: 1;
    }
    
    &__head {
        margin: 0 0 math-clamp(10, 11);
    }
    
    &__type {
        margin-bottom: math-clamp(11, 12);
        font-weight: 350;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.4;
        text-transform: capitalize;
    }
    
    &__name {
        margin: 0;
        padding: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        line-clamp: 3;
        -webkit-box-orient: vertical;
    }
    
    &__description {
        @extend %transition;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        height: 100%;
        opacity: 0;
    }
    
    &__text {
        font-weight: 400;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.6;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        line-clamp: 4;
        -webkit-box-orient: vertical;
    }
    
    &__underline-link {
        margin: rem(11) 0;
        @media (max-width: 767px) {
            margin-bottom: 23px;
        }
    }
}
