@import "variables";

%h {
    clear: both;
    margin: 0 0 math-clamp(23, 33);
    padding: 0;
    line-height: 1.1;
    font-weight: 700;
    
    &:first-child {
        padding-top: 0;
    }
}

%h1 {
    font-size: math-clamp(36, 64);
}
%h2 {
    font-size: math-clamp(36, 50);
}
%h3 {
    font-family: $font-family-p22-mackinac-pro;
    font-weight: 500;
    font-size: rem(28);
    line-height: 1.3;
}
%h4 {
    font-size: rem(16);
    line-height: 1.5;
}
%h5 {
    font-size: rem(24);
}
%h6 {
    font-size: rem(20);
}
%transition {
    transition: .4s ease-in-out;
}
%logo {
    svg,
    img {
      width: 231px;
      height: auto;
      @media (max-width: 991px) {
        width: sd(231px);
      }
      @media (max-width: 767px) {
        width: 125px;
      }
    }
}
%green-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: math-clamp(18, 18) math-clamp(20, 24);
  font-weight: 700;
  font-size: math-clamp(14, 16);
  color: $white;
  line-height: 1;
  background-color: $coregreen;
  border: 0;
  border-radius: rem(8);
  text-align: center;
  min-width: math-clamp(157, 157);
  text-decoration: none;

  @media (hover: hover) {
    &:hover {
      background-color: $salmon;
      border-radius: math-clamp(117, 117);
      color: $black;
    }
  }
}
