@import "variables";

.gs-header {
    $header: &;
    
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    height: math-clamp(46, 85);
    z-index: 100;
    transition: box-shadow .4s ease-in-out;
    background-color: $white;
    
    .admin-bar & {
        top: 32px;
    }
    
    &__overlay {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: transparent;
        z-index: 1;
        
        &_active {
            display: block;
        }
        
        @media (max-width: 991px) {
            display: none !important;
            z-index: -1;
        }
    }
    
    &__container {
        position: relative;
        padding: 0 rem(20);
        max-width: rem(1364);
        width: 100%;
        height: 100%;
        z-index: 2;
    }
    
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        
        & > *:nth-child(n+2) {
            margin-left: rem(20);
            @media (max-width: 991px) {
                margin-left: 0;
            }
        }
    }
    
    &__logo {
        @extend %logo;
    }
    
    &__end {
        @extend %transition;
        display: flex;
        align-items: center;
        height: 100%;
        @media (max-width: 991px) {
            position: absolute;
            flex-direction: column;
            align-items: flex-start;
            top: 100%;
            left: 0;
            padding: 27px 40px 57px;
            width: 100%;
            height: 100vh;
            overflow-y: auto;
            background-color: $white;
            transform: translateY(calc(-100% - 46px));
            
            #{$header} {
                &__sign-in {
                    display: none;
                }
            }
        }
        
        &_active {
            transform: translateY(0);
        }
    }
    
    &__menu {
        display: flex;
        align-items: center;
        list-style: none;
        margin: 0;
        height: 100%;
        @media (max-width: 991px) {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 25px;
            width: 100%;
            height: auto;
        }
        
        & > * {
            &:nth-child(n+2) {
                margin-left: rem(30);
                @media (max-width: 991px) {
                    margin-left: 0;
                }
            }
        }
        
        & > .menu-item {
            @extend %transition;
            position: relative;
            display: flex;
            align-items: center;
            height: 100%;
            box-shadow: inset 0 rem(-3) 0 0 transparent;
            @media (max-width: 991px) {
                flex-direction: column;
                align-items: flex-start;
                padding: 24px 0 0 0;
                width: 100%;
                border-top: 2px solid $grey;
                
                &:last-child {
                    border-bottom: rem(2) solid $grey;
                }
            }
            
            & > a {
                position: relative;
                font-weight: 700;
                font-size: rem(16);
                color: $black;
                line-height: 1;
                transition: color .4s ease-in-out;
                text-decoration: none;
                
                @media (min-width: 992px) {
                    height: 100%;
                    display: flex;
                    align-items: center;
                }
                
                @media (max-width: 991px) {
                    display: inline-flex;
                    margin-bottom: 24px;
                }
            }
            
            @media (hover: hover) {
                &:hover {
                    box-shadow: inset 0 rem(-3) 0 0 $coregreen;
                    
                    & > a {
                        color: $coregreen;
                    }
                }
            }
    
            &-has-children {
                & > a {
                    padding-right: rem(25);
                    text-decoration: none;
                    
                    &::before {
                        content: '';
                        position: absolute;
                        top: 48%;
                        right: 0;
                        width: rem(10);
                        height: rem(10);
                        border: rem(2) solid $green;
                        border-left: 0;
                        border-top: 0;
                        transform: translateY(-50%) rotate(45deg);
                        transition: all .3s;
                    }
                }
            }
            
            &_active {
                & > a {
                    text-decoration: none;
                    
                    &::before {
                        transform: translateY(-50%) rotate(-135deg);
                    }
                }
                
                .sub-menu {
                    display: flex;
                    @media (max-width: 991px) {
                        height: auto;
                    }
                }
            }
        }
        
        .sub-menu {
            list-style: none;
            position: absolute;
            top: calc(100% - #{rem(8)});
            left: rem(-28);
            display: none;
            flex-direction: column;
            gap: rem(14);
            margin: 0;
            padding: rem(24) rem(14) rem(24) rem(24);
            width: rem(385);
            border-top: rem(4) solid $coregreen;
            background-color: $white;
            box-shadow: 0px rem(8) rem(20) rgba($black2, 0.1);
            border-radius: rem(4);
            overflow: hidden;
            z-index: 1;
            @media (max-width: 991px) {
                position: static;
                padding: 0;
                width: 100%;
                height: 0;
                overflow: hidden;
                transition: height .4s ease-in-out;
                box-shadow: unset;
                border-top: 0;
            }
            
            & > .menu-item {
                @media (max-width: 991px) {
                    &:last-child {
                        margin-bottom: 28px;
                    }
                }
                
                & > a {
                    display: flex;
                    align-items: flex-start;
                    gap: rem(20);
                    text-decoration: none;
                    
                    & > div {
                        &:first-child {
                            position: relative;
                            
                            &::after {
                                @extend %transition;
                                content: '';
                                position: absolute;
                                top: calc(100% + #{rem(8)});
                                left: 0;
                                width: rem(9);
                                height: rem(2);
                                background-color: $green;
                            }
                            
                            svg {
                                @extend %transition;
                                width: rem(24);
                                height: auto;
                                
                                path {
                                    @extend %transition;
                                }
                            }
                        }
                        
                        &:last-child {
                            display: flex;
                            flex-direction: column;
                            
                            span {
                                font-weight: 400;
                                font-size: rem(14);
                                color: $black;
                                line-height: 1.7;
                                
                                &:first-child {
                                    @extend %transition;
                                    font-weight: 700;
                                }
                            }
                        }
                    }
                    
                    @media (hover: hover) {
                        &:hover {
                            & > div {
                                &:first-child {
                                    
                                    path {
                                        stroke: $salmon;
                                    }
                                    
                                    &::after {
                                        width: 100%;
                                        background-color: $salmon;
                                    }
                                }
                                
                                span {
                                    &:first-child {
                                        color: $coregreen;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    
    &__sign-in {
        margin-left: rem(42);
        display: flex;
        align-items: center;
        font-weight: 700;
        font-size: rem(16);
        line-height: 1;
        color: $coregreen;
        text-decoration: none;
        
        & > *:nth-child(n+2) {
            margin-left: rem(10);
        }
        
        svg,
        img {
            width: rem(27);
            height: auto;
            @media (max-width: 991px) {
                margin-left: 0;
                display: none;
            }
        }
    }
    
    &__btn {
        margin-left: rem(18);
        @media (max-width: 991px) {
            margin-left: 0;
        }
    }
    
    &__mobile {
        display: none;
        align-items: center;
        @media (max-width: 991px) {
            display: flex;
        }
        
        & > *:nth-child(n+2) {
            margin-left: rem(10);
        }
    }
    
    &__burger {
        position: relative;
        width: rem(19);
        height: rem(12);
        cursor: pointer;
        
        span {
            @extend %transition;
            position: absolute;
            left: 0;
            width: 100%;
            height: rem(2);
            background-color: $coregreen;
            border-radius: rem(8);
            transform-origin: center;
            
            &:first-child {
                top: 0;
            }
            
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
            }
            
            &:last-child {
                top: calc(100% - #{rem(2)});
            }
        }
        
        &_active {
            span {
                &:first-child {
                    top: 50%;
                    transform: translateY(-50%) rotate(135deg);
                }
                
                &:nth-child(2) {
                    opacity: 0;
                    transform: translateX(-100%);
                }
                
                &:last-child {
                    top: 50%;
                    transform: translateY(-50%) rotate(-135deg);
                }
            }
        }
    }
    
    &_is-scrolling {
        box-shadow: 0px rem(8) rem(20) rgba(0, 0, 0, 0.1);
    }
}
