.gs-compare {
    $comp: &;
    
    padding: math-clamp(62, 76) 0 math-clamp(91, 113);
    overflow: hidden;
    
    &_with-rounded-bg {
        position: relative;
        background-color: $lightgrey;
        overflow: visible;
        @media (max-width: 991px) {
            overflow: hidden;
        }
        
        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: calc(100% + #{rem(120)});
            background-color: $white;
            border-radius: rem(500) 0 0 0;
            @media (max-width: 767px) {
                height: calc(100% + 387px);
            }
        }
    }
    
    .gs-container {
        position: relative;
        z-index: 5;
    }
    
    &__container {
        position: relative;
        z-index: 5;
        
        @media (max-width: 767px) {
            width: calc(100% - 120px);
            padding: 0;
        }
    }
    
    &__header {
        margin-bottom: math-clamp(60, 102);
    }
    
    &__title {
        margin: 0 auto;
        max-width: rem(864);
        text-align: center;
    }
    
    &__more {
        display: flex;
        justify-content: center;
        margin-top: rem(40);
    }
    
    &__main {
        position: relative;
        max-width: 1130px;
        margin-left: auto;
        margin-right: auto;

        @media (min-width: 992px) {
            display: flex;
            justify-content: center;
        }
    }
    
    &__filter {
        margin-top: math-clamp(38, 70);
        margin-bottom: math-clamp(35, 80);
        filter: drop-shadow(.25rem .25rem 1.5625rem rgba(0,0,0,.15)) !important;
    
        .gs-filter {
            
            &__list {
                display: inline-flex;
            }
        }
    }
    
    &__tab {
        display: none;
        position: relative;
        @media (max-width: 991px) {
            flex-wrap: wrap;
        }
        
        @media (min-width: 992px) {
            box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
            border-radius: math-clamp(32, 47);
        }
        
        &.active {
            display: flex;
        }
    }
    
    &__start {
        flex: 0 0 100%;
        max-width: 100%;
        
        @media (min-width: 992px) {
            // flex-basis: 0;
            // flex-grow: 1;
            // max-width: 100%;
            flex: 0 0 auto;
            min-width: 200px;
        }
        @media (min-width: 1200px) {
            min-width: 300px;
        }
        @media (max-width: 991px) {
            box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
            border-radius: math-clamp(32, 47);
            background-color: $white;
        }
        
        &-header {
            text-align: center;
            font-weight: 600;
            font-size: 16px;
            line-height: 30px;
            padding-top: math-clamp(17, 28);
            padding-bottom: math-clamp(7, 30);
            
            @media (min-width: 992px) {
                border-bottom: 2px solid $grey;
                text-align: right;
                padding-right: 40px;
            }
        }
        
        &-content {
            padding-bottom: 260px;
            
            @media (min-width: 992px) {
                padding-top: 20px;
                padding-bottom: 20px;
            }
        }
        
        &-list {
            font-weight: 400;
            font-size: 14px;
            line-height: 32px;
            text-align: center;
            
            @media (min-width: 992px) {
                text-align: right;
                padding-right: 40px;
            }
        }
    }
    
    &__list {
        $li: &;
        
        // flex: 0 0 183px;
        // max-width: 183px;
        min-width: 183px;
        position: relative;
        border-right: 2px solid $grey;
        
        &:nth-last-child(2) {
            border-right: none;
        }
        
        &_active {
            // flex: 0 0 235px;
            // max-width: 235px;
            min-width: 235px;
            border-right: none;
            
            #{$li} {
                &-content {
                    background-color: $white;
                    position: absolute;
                    left: 0;
                    top: -17px;
                    bottom: -17px;
                    width: 100%;
                    box-shadow: 1px 1px 30px rgba(0, 0, 0, 0.25);
                    border-radius: 32px;
                }
                
                &-head {
                    height: 106px;
                    position: relative;
                    background-color: $highlighter;
                    border-bottom: none;
                    border-radius: 32px 32px 0 0;
                    
                    svg {
                        display: block;
                        position: absolute;
                        top: -28px;
                        right: -30px;
                    }
                }
                
                &-vals {
                    color: $coregreen;
                    font-weight: 700;
                }
            }
        }
        
        @media (max-width: 991px) {
            display: none;
        }
        
        &-content {
            position: relative;
            z-index: 5;
        }
        
        &-head {
            height: 90px;
            border-bottom: 2px solid $grey;
            display: flex;
            align-items: center;
            justify-content: center;
            
            img {
                width: auto;
                max-height: 55px;
            }
        }
        
        &-vals {
            padding: 20px 15px;
            font-weight: 400;
            font-size: 14px;
            line-height: 32px;
            text-align: center;
        }
    }
    
    &__slider {
        margin-top: -260px;
        width: calc(100% + 120px);
        margin-left: -60px;
        
        @media (min-width: 992px) {
            display: none;
        }
        
        .swiper {
            padding: 32px 0;
        }
        
        .swiper-slide {
            width: 209px;
        }
        
        &-nav {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            gap: 24px;
            margin-top: 25px;
        }
    }
    
    &__item {
        $it: &;
        
        box-shadow: 1px 1px 30px rgba(0, 0, 0, 0.25);
        border-radius: 32px;
        background-color: $white;
        
        &_active {
            position: relative;
            z-index: 5;
            
            #{$it} {
                &-head {
                    background-color: $highlighter;
                    position: relative;
                    border-radius: 32px 32px 0 0;
                    
                    svg {
                        width: 44px;
                        height: auto;
                        display: block;
                        position: absolute;
                        top: -3px;
                        right: -9px;
                    }
                }
                
                &-vals {
                    color: $coregreen;
                    font-weight: 700;
                }
            }
        }
        
        &-head {
            height: 85px;
            display: flex;
            align-items: center;
            justify-content: center;
            
            img {
                width: auto;
                max-width: 137px;
                max-height: 45px;
            }
        }
        
        &-vals {
            padding: 30px 15px;
            font-weight: 400;
            font-size: 14px;
            line-height: 32px;
            text-align: center;
        }
    }
}
