.gs-hero-with-description {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: math-clamp(347, 550);
  color: $white;
  text-align: center;
  background-color: $coregreen;
  visibility: hidden;

  &__content {
    padding: rem(50) 0;
  }

  &__title {
    margin: 0 0 rem(25);
    padding: 0;
  }

  &__subtitle {
    margin: 0 0 rem(10);
    font-weight: 700;
    font-size: math-clamp(20, 24);
    line-height: 1.3;
  }

  &__text {
    font-weight: 400;
    font-size: math-clamp(14, 16);
    line-height: 1.6;
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
  }
}
