.gs-soc2-block {
    $soc: &;
    
    width: calc(100% - 60px);
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
    gap: math-clamp(30, 40);
    padding: 35px 0;
    justify-content: space-between;
    align-items: center;
    
    @media (max-width: 767px) {
        flex-direction: column;
    }
    
    &_divider {
        border-top: 2px solid $grey;
    }
    
    &__content {
        flex: 0 0 100%;
        max-width: 100%;
        
        @media (min-width: 768px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 640px;
        }
        @media (max-width: 767px) {
            text-align: center;
        }
        
        h1, h2, h3, h4, h5, h6 {
            margin-bottom: 10px;
        }
        
        a {
            color: $coregreen;
            text-decoration: none;
            
            &:hover {
                text-decoration: underline;
            }
        }
        
        > * {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    
    &__img {
        flex: 0 0 120px;
        max-width: 120px;
    }
}
