@import "../variables";

.gs-resources-post-big {
    $rpb: &;
    
    position: relative;
    
    &_default {
        padding: math-clamp(50, 100) 0;
        
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $lightgrey;
            border-radius: 0 0 0 math-clamp(400, 800);
        }
    
        #{$rpb} {
            &__container {
                display: flex;
                flex-wrap: wrap;
                gap: math-clamp(40, 85);
            }
            
            &__poster {
                flex: 0 0 100%;
                max-width: 100%;
                height: 345px;
                border-radius: 4px;
                overflow: hidden;
                
                @media (min-width: 992px) {
                    flex: 0 0 550px;
                    max-width: 550px;
                }
                
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            
            &__content {
                flex: 0 0 100%;
                max-width: 100%;
                color: $black;
                
                @media (min-width: 992px) {
                    flex-basis: 0;
                    flex-grow: 1;
                    max-width: 100%;
                }
    
                .gs-underline-link {
                    & > span {
                        &::after {
                            background-color: $green;
                        }
                    }
                }
            }
            
            &__type {
                font-weight: 700;
                font-size: 16px;
                line-height: 22px;
                color: $coregreen;
                margin-bottom: math-clamp(20, 30);
            }
            
            &__title {
                margin-bottom: 24px;
            }
            
            &__text {
                font-weight: 400;
                font-size: 16px;
                line-height: 26px;
                margin-bottom: 25px;
            }
        }
    }
    
    &_video {
        padding-bottom: math-clamp(50, 100);
        color: $coregreen;
    
        &:before {
            content: '';
            position: absolute;
            top: math-clamp(40, 85);
            left: 0;
            bottom: 0;
            width: 100%;
            background-color: $lightgrey;
            border-radius: 0 0 math-clamp(300, 500) 0;
        }
        
        #{$rpb} {
            &__poster {
                width: 100%;
                height: math-clamp(400, 575);
                border-radius: 4px;
                overflow: hidden;
                position: relative;
                margin-bottom: math-clamp(40, 75);
    
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                
                svg {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
    
            &__type {
                font-weight: 700;
                font-size: 16px;
                line-height: 22px;
                color: $coregreen;
                margin-bottom: 18px;
            }
    
            &__text {
                font-weight: 400;
                font-size: 16px;
                line-height: 26px;
                margin-bottom: math-clamp(25, 35);
                max-width: 550px;
            }
        }
    }
    
    &__container {
        width: calc(100% - 72px);
        max-width: 1180px;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        z-index: 5;
    }
}
