.gs-embeded-walkthrough-with-accord {
  $ewwa: &;

  position: relative;
  padding: math-clamp(0, 96) 0 math-clamp(68, 108);

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    @media (max-width: 767px) {
      align-items: flex-start;
    }
  }

  &__right {
    width: 50%;
    height: 100%;
    background-color: $lightgrey;
    border-radius: 0 0 rem(700) 0;
    @media (max-width: 767px) {
      width: 100%;
      height: 458px;
      border-radius: 0 700px 0 0;
    }
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__start {
    width: 50%;
    padding-right: math-clamp(60, 100);
    @media (max-width: 767px) {
      order: 1;
      padding-right: 0;
      width: 100%;
    }
  }

  &__end {
    margin-bottom: rem(59);
    width: 50%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }

  &__demo {
    display: block;
    margin: 0 auto;
    width: math-clamp(235, 291);
    border-radius: rem(50);
  }

  &__title {
    margin-bottom: math-clamp(31, 31);
    @media (max-width: 767px) {
      margin-bottom: 47px;
      text-align: center;
    }
  }

  &__item {
    border-bottom: rem(2) solid $lightgrey;

    &:nth-child(n+2) {
      margin-top: 25px;
    }

    &_active {
      #{$ewwa} {
        &__toggler {
          transform: rotate(-180deg);
        }

        &__icon {
          path {
            stroke: $salmon;
          }
    
          &::before {
            width: rem(27);
            background-color: $salmon;
            @media (max-width: 767px) {
              width: 92px;
            }
          }
        }
      }
    }
  }

  &__head {
    margin-bottom: rem(18);

    &-bot {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__icon {
    margin-bottom: math-clamp(17, 20);
    position: relative;
    padding-bottom: rem(17);
    width: rem(43);

    svg {
      width: 100%;
      height: auto;

      path {
        @extend %transition;
      }
    }

    &:before {
      @extend %transition;
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: rem(15);
      height: rem(2);
      background-color: $green;
    }
  }

  &__toggler {
    @extend %transition;
    margin-left: rem(20);
    width: rem(32);
    cursor: pointer;
    transform-origin: center;
    
    svg {
      width: 100%;
      height: auto;
    }
  }

  &__name {
    font-weight: 700;
    font-size: rem(16);
    color: $coregreen;
    line-height: 1.5;
  }

  &__textual {
    padding-bottom: rem(18);
    font-weight: 400;
    font-size: rem(14);
    color: $black;
    line-height: 1.6;

    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__body {
    @extend %transition;
    height: 0;
    overflow: hidden;
  }
}