.gs-logos {
    padding: math-clamp(20, 42) 0 math-clamp(30, 65);
    background-color: $white;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 5;
    
    &_transparent {
        box-shadow: none;
        background-color: transparent;
    }
    
    .slick {
        &-track {
            display: flex;
            align-items: center;
        }
    }
    
    &__title {
        margin-bottom: math-clamp(11, 28);
        font-weight: 700;
        font-size: math-clamp(16, 24);
        color: $black;
        line-height: 1.3;
        text-transform: uppercase;
        text-align: center;
    }
    
    &__wr {
        padding: 0 math-clamp(28, 63);
    }
    
    &__img {
        max-width: math-clamp(88, 188);
        filter: grayscale(1);
    }
}
