.gs-approach {
    padding: math-clamp(50, 69) 0 0;
    background-color: $lightgrey;
    
    > .gs-container {
        position: relative;
        z-index: 5;
    }
    
    &_bottom-right-circle {
        background-color: transparent;
        position: relative;
        
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: calc(100% - 30px);
            background-color: $lightgrey;
            border-radius: 0 0 300px 0;
            
            @media (min-width: 768px) {
                height: 66.5%;
                border-radius: 0 0 500px 0;
            }
        }
    }
    
    &__content {
        border-bottom: rem(2) solid $grey;
    }
    
    &__header {
        margin-bottom: math-clamp(17, 70);
    }
    
    &__textual {
        margin: 0 auto;
        max-width: rem(900);
        text-align: center;
    }
    
    &__title {
        margin: 0
    }
    
    &__text {
        margin-top: rem(23);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $black;
        line-height: 1.5;
    }
    
    &__more {
        margin-top: rem(35);
    }
    
    &__main {
        max-width: 900px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: math-clamp(46, 76);
    }
}
