.gs-pre-footer-section {
    $fwd: &;
    
    position: relative;
    padding-top: rem(90);
    background-color: $green;
    @media (max-width: 767px) {
        padding-top: 95px;
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: 100%;
    }
    
    &__left {
        width: 71%;
        height: calc(100% - #{rem(100)});
        background-color: $coregreen;
        @media (max-width: 767px) {
            width: 100%;
            height: 68%;
        }
    }
    
    &__right {
        flex: 1 1 auto;
        height: calc(100% - rem(100));
        background-color: transparent;
        @media (max-width: 767px) {
            width: 100%;
            height: 26%;
            background-color: $green;
        }
    }
    
    &__bot {
        width: 100%;
        height: rem(100);
        background-color: $white;
        
        @media (max-width: 767px) {
            width: 100%;
            height: 6%;
        }
        
        &_black {
            background-color: $black;
            height: 105px;
    
            @media (max-width: 767px) {
                height: 55px;
            }
        }
    }
    
    &__content {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    
    &__start {
        padding: rem(100) 0;
        width: 42%;
        @media (max-width: 767px) {
            margin-bottom: 95px;
            padding: 0;
            width: 100%;
        }
    }
    
    &__end {
        max-width: 55%;
        width: rem(650);
        @media (max-width: 767px) {
            max-width: 100%;
            width: 100%;
        }
    }
    
    &__textual {
        max-width: rem(447);
        padding-right: rem(30);
        
        @media (max-width: 767px) {
            padding-right: 30px;
        }
        
        &_full {
            max-width: 100%;
        }
    }
    
    &__text {
        margin-bottom: rem(45);
        font-weight: 400;
        font-size: math-clamp(20, 24);
        color: $white;
        line-height: 1.6;
        @media (max-width: 767px) {
            margin-bottom: 37px;
        }
        
        p {
            margin-bottom: rem(20);
            
            &:last-child {
                margin-bottom: 0;
            }
        }
        
    }
    
    &__title {
        margin-bottom: rem(41);
        color: $white;
        @media (max-width: 767px) {
            margin-bottom: 48px;
        }
    }
    
    &__poster {
        position: relative;
        width: rem(650);
        max-width: 100%;
        border-radius: math-clamp(2, 4) math-clamp(73, 150) math-clamp(2, 4) math-clamp(2, 4);
        overflow: hidden;
        @media (max-width: 767px) {
            margin: 0 auto;
        }
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__pure-img {
        margin: 0 auto;
        max-width: 100%;
        height: auto;
    }
    
    &__style-1 {
        #{$fwd} {
            &__subscribe {
                &-text {
                    color: $white;
                }
            }
        }
    }
    
    &__style-2 {
        //display: none;
    }
    
    &__subscribe {
        padding: rem(20) 0 math-clamp(73, 106);
        background-color: $lightgrey;
        
        &-title {
            margin-bottom: rem(54);
            text-align: center;
        }
        
        &-textual {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
        
        &-start {
            width: 42%;
            @media (max-width: 767px) {
                margin-bottom: 50px;
                width: 100%;
            }
        }
        
        &-end {
            width: 42%;
            @media (max-width: 767px) {
                width: 100%;
            }
        }
        
        &-text {
            font-weight: 400;
            font-size: math-clamp(20, 24);
            color: $black;
            line-height: 1.5;
        }
        
        &-btn {
            @media (max-width: 767px) {
                margin: 0 auto;
            }
        }
        
        &-s1 {
        
        }
    }
    
    &__downloads {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        
        a {
            margin: 0 rem(20) rem(20) 0;
        }
        
        svg {
            width: rem(130);
            height: auto;
        }
    }
    
    &_reverse {
        #{$fwd} {
            &__bg {
                flex-direction: row-reverse;
            }
            
            &__content {
                flex-direction: row-reverse;
            }
        }
    }
    
    &_without-mask {
        padding: rem(29) 0;
        
        #{$fwd} {
            &__left {
                height: 100%;
                @media (max-width: 767px) {
                    height: 71%;
                }
            }
            
            &__right {
                height: 100%;
                @media (max-width: 767px) {
                    height: 29%;
                }
            }
            
            &__content {
                align-items: center;
            }
            
            &__pure-img {
                max-width: 395px;
                margin-left: auto;
                margin-right: auto;
                display: block;
            }
        }
    }
    
    &_light-version {
        padding-top: 0;
        background-color: $lightgrey;
    
        .hbspt-form {
            margin-top: 0 !important;
        }
    }
    
    .hbspt-form {
        width: 100%;
        display: block;
        background: $white;
        box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
        border-radius: 8px;
        margin-top: 40px;
        padding: 20px 20px 20px 30px;
        
        form {
            display: flex;
            flex-wrap: wrap;
            
            .hs-form-field {
                flex-basis: 0;
                flex-grow: 1;
                max-width: 100%;
                
                label {
                    display: none;
                }
                
                .input {
                    padding-right: 17px;
                    
                    input {
                        width: 100%;
                        height: 52px;
                        line-height: 52px;
                        border: none;
                        background-color: transparent;
                        padding: 0;
                        border-bottom: 2px solid $grey;
                        
                        &.invalid {
                            border-color: #ff0000;
                        }
                    }
                }
            }
            
            .hs-submit {
                flex: 0 0 auto;
                width: auto;
                max-width: 100%;
                
                .hs-button {
                    @extend %transition;
                    @extend %green-btn;
                    
                    width: 157px;
                    height: 52px;
    
                    @media (hover: hover) {
                        &:hover {
                            background-color: $salmon;
                            border-radius: rem(117);
                            color: $black;
                        }
                    }
                }
            }
            
            .hs-error-msgs {
                display: none;
            }
        }
    }
}
