.gs-hero-4 {
    position: relative;
    color: $white;
    visibility: hidden;
    z-index: 5;
    
    .gs-contact-form-page_is-preview & {
        visibility: visible !important;
    }
    
    + .gs-info-blocks {
        margin-top: -115px;
        
        @media (min-width: 1200px) {
            margin-top: -215px;
        }
    }
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        width: 100%;
        height: 100%;
        @media (max-width: 767px) {
            flex-wrap: wrap;
        }
    }
    
    &__left {
        flex: 1 1 auto;
        height: 100%;
        background-color: $coregreen;
        @media (max-width: 767px) {
            width: 100%;
            height: 300px;
        }
    }
    
    &__right {
        width: 50%;
        height: 100%;
        background-color: $green;
        @media (max-width: 767px) {
            width: 100%;
            height: 500px;
        }
    }
    
    &__content {
        position: relative;
        z-index: 1;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        min-height: 700px;
    }
    
    &__start {
        padding-right: rem(101);
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media (max-width: 767px) {
            padding-right: 0;
            width: 100%;
            height: 300px;
        }
    }
    
    &__end {
        width: 50%;
        @media (max-width: 767px) {
            width: 100%;
            height: 500px;
        }
    }
    
    &__poster {
        width: 100%;
        height: 100%;
        border-radius: 0 rem(400) rem(400) 0;
        overflow: hidden;
    }
    
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__title {
        padding: 0;
    }
    
    &__text {
        font-weight: 400;
        font-size: math-clamp(20, 24);
        line-height: 1.5;
    }
}

.gs-info-blocks {
    position: relative;
    background-color: $lightgrey;
    
    &__container {
        width: calc(100% - 72px);
        max-width: 1180px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        gap: math-clamp(30, 60);
    }
    
    &__item {
        $itm: &;
        
        background: $white;
        box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        padding: 45px 30px 40px;
        flex: 0 0 100%;
        max-width: 100%;
        min-height: 350px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        z-index: 15;
        
        @media (min-width: 1200px) {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 100%;
        }
        
        @media (hover: hover) {
            &:hover {
                #{$itm} {
                    &-icon:not(.gs-info-blocks__item-icon_fill) {
                        path {
                            stroke: $salmon;
                        }
                    }
                    
                    &-icon_fill {
                        path {
                            fill: $salmon;
                        }
                    }
                    
                    &-description {
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
            }
        }
        
        &-icon {
            width: 82px;
            height: 52px;
            margin-bottom: 10px;
            
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
                object-position: left center;
            }
            
            svg {
                max-width: 100%;
                width: auto;
                max-height: 100%;
                height: auto;
                
                path {
                    transition: all .4s;
                }
            }
        }
        
        &-title {
            font-weight: 700;
            font-size: rem(28);
            line-height: 52px;
            margin-bottom: 7px;
            
            span {
                display: inline;
                position: relative;
                background-image: linear-gradient(#d6fcb2, #d6fcb2);
                background-position: 0 calc(100% - 5px);
                background-repeat: no-repeat;
                background-size: 100% 10px;
                transition: background-size .3s;
            }
        }
        
        &-description {
            font-weight: 400;
            font-size: rem(16);
            line-height: 26px;
            
            @media (min-width: 1200px) {
                opacity: 0;
                transform: translateY(-10px);
                transition: all .5s;
            }
        }
        
        a {
            display: block;
            position: relative;
            width: max-content;
            font-weight: 700;
            font-size: rem(16);
            line-height: 30px;
            color: $coregreen;
            text-decoration: none;
            
            &:before {
                content: '';
                position: absolute;
                top: calc(100% + 3px);
                left: 0;
                width: 17px;
                transition: all .4s;
                height: 2px;
                background-color: $green;
            }
            
            @media (hover: hover) {
                &:hover {
                    &:before {
                        width: 100%;
                    }
                }
            }
        }
    }
}
