@keyframes gs-arrow-up-down-move {
    0% {
        transform: translateY(-20%);
    }
    100% {
        transform: translateY(0);
    }
}

[data-animation=ladder-text] {
    .met-word {
        position: relative;
        overflow: hidden;
        display: inline-block;
        line-height: 1;
        padding-bottom: 5px;
        
        &__wr {
            display: inline-block;
            position: relative;
            transform: translateY(120%);
        }
    }
}

[data-animation=to-right-ladder-short] {
    & > * {
        transform: translateX(50px);
        opacity: 0;
    }
}

[data-animation=from-bottom-right-angle] {
    & > * {
        transform: rotate(45deg);
        opacity: 0;
        transform-origin: bottom right;
    }
}

.swiper {
    &-controls {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: rem(20);
        min-height: rem(16);
    }
    
    &-navigation {
        display: flex;
        
        & > *:nth-child(n+2) {
            margin-left: rem(31);
        }
    }
    
    &-button {
        &-prev,
        &-next {
            @extend %transition;
            
            svg {
                width: rem(13);
                height: auto;
                
                path {
                    @extend %transition;
                }
            }
            
            &:hover {
                path {
                    stroke: $salmon;
                }
            }
        }
        
        &-disabled {
            opacity: 0.5;
            cursor: default;
            pointer-events: none;
        }
    }
    
    &-pagination {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        max-width: rem(100);
        
        & > *:nth-child(n+2) {
            margin-left: rem(10);
        }
        
        &-bullet {
            width: rem(10);
            height: rem(10);
            border-radius: 50%;
            background-color: $green;
            
            &-active {
                background-color: $coregreen;
            }
        }
    }
}

.gs-container {
    margin: 0 auto;
    max-width: rem(1191);
    box-sizing: content-box;
    padding: 0 36px;
}

.gs-section-default-element {
    &:not(.alignwide):not(.alignfull) {
        margin-left: auto;
        margin-right: auto;
        width: calc(100% - 72px);
        max-width: 800px;
    }
    
    &.alignwide {
        max-width: 1400px;
    }
}

.gs-btn {
    @extend %transition;
    @extend %green-btn;
    
    @media (hover: hover) {
        &:hover {
            background-color: $salmon;
            border-radius: rem(117);
            color: $black;
        }
    }
    
    &_white {
        background-color: $white;
        color: $coregreen;
    }
    
    &_long {
        width: rem(189);
        max-width: 100%;
    }
}


.gs-title {
    
    &_with-highlight {
        span {
            text-decoration: none;
            background-image: linear-gradient($highlighter, $highlighter);
            background-position: 0 calc(100% - #{math-clamp(5, 10)});
            background-repeat: no-repeat;
            background-size: 100% rem(10);
            transition: background-size .3s;
        }
    }
    
    &_with-green {
        span {
            color: $green;
        }
    }
}

.gs-underline-link {
    $ul: &;
    
    position: relative;
    display: inline-flex;
    align-items: center;
    font-weight: 700;
    font-size: math-clamp(14, 16);
    color: $coregreen;
    line-height: 1;
    white-space: nowrap;
    text-decoration: none;
    
    @media (hover: hover) {
        &:hover {
            & > span {
                &::after {
                    width: 100%;
                    background-color: $salmon;
                }
            }
            
            #{$ul} {
                &__arrow {
                    path {
                        stroke: $salmon;
                    }
                }
            }
        }
    }
    
    & > span {
        position: relative;
        padding-bottom: rem(10);
        
        &::after {
            @extend %transition;
            content: '';
            position: absolute;
            top: calc(100% - #{rem(2)});
            left: 0;
            width: rem(32);
            height: rem(2);
            background-color: $highlighter;
        }
    }
    
    &__arrow {
        display: none;
        margin-left: rem(15);
        width: rem(10);
        height: auto;
        animation: gs-arrow-up-down-move .6s ease-in-out infinite alternate;
        
        path {
            @extend %transition;
        }
    }
    
    &_lightgreen {
        color: $highlighter;
        
        @media (hover: hover) {
            &:hover {
                & > span {
                    &::after {
                        width: 100%;
                        background-color: $highlighter;
                    }
                }
                
                #{$ul} {
                    &__arrow {
                        path {
                            stroke: $highlighter;
                        }
                    }
                }
            }
        }
        
        #{$ul} {
            &__arrow {
                path {
                    stroke: $highlighter;
                }
            }
        }
        
        & > span {
            &::after {
                background-color: $highlighter;
            }
        }
    }
    
    &_active {
        & > span {
            &::after {
                width: 100%;
                background-color: $salmon;
            }
        }
    }
    
    &_with-arrow {
        #{$ul} {
            &__arrow {
                display: block;
            }
        }
    }
}

.gs-text-with-sidebar {
    $tws: &;
    
    &:not(.gs-text-with-sidebar_simplified) {
        margin-bottom: math-clamp(114, 162);
    }
    
    &__content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        @media (max-width: 767px) {
            flex-direction: column;
            justify-content: flex-start;
        }
    }
    
    &__start {
        width: 32%;
        @media (max-width: 767px) {
            margin-bottom: 50px;
            width: 100%;
        }
    }
    
    &__end {
        padding-top: rem(150);
        width: 62%;
        @media (max-width: 767px) {
            padding-top: 0;
            width: 100%;
        }
    }
    
    &__panel {
        position: relative;
        background-color: $lightgrey;
        border-radius: 0 0 rem(150) 0;
        
        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 100%;
            width: 200%;
            height: 100%;
            background-color: $lightgrey;
        }
        
        &-wr {
            direction: rtl;
            padding: rem(90) rem(66) rem(126) 0;
            max-height: 100vh;
            overflow: auto;
            @media (max-width: 767px) {
                max-height: unset;
                overflow: hidden;
            }
        }
        
        &-title {
            font-weight: 700;
            font-size: rem(28);
            color: $black;
            line-height: 1.9;
            direction: ltr;
        }
    }
    
    &__item {
        direction: ltr;
        margin-top: rem(26);
        
        &_active {
            #{$tws} {
                &__link {
                    font-weight: 700;
                    
                    &::after {
                        background-color: $green;
                    }
                }
            }
        }
    }
    
    &__link {
        @extend %transition;
        display: block;
        position: relative;
        font-weight: 400;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.9;
        text-decoration: none;
        
        &::after {
            @extend %transition;
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: rem(17);
            height: rem(2);
            background-color: transparent;
        }
        
        &:hover {
            font-weight: 700;
            
            &::after {
                background-color: $green;
            }
        }
    }
    
    &__simplified {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        gap: 40px;
        padding-top: math-clamp(70, 130);
        padding-bottom: math-clamp(80, 150);
        
        @media (min-width: 768px) {
            justify-content: space-between;
        }
        
        &-col {
            flex-basis: 0;
            flex-grow: 1;
            max-width: 675px;
            
            .gs-btn {
                margin-top: 65px;
            }
        }
        
        &-image {
            flex: 0 0 380px;
            max-width: 380px;
            height: 600px;
            overflow: hidden;
            border-radius: 4px 4px 4px 150px;
    
            @media (min-width: 768px) {
                margin-top: 55px;
            }
            
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.gs-clipboard {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: -1;
    pointer-events: none;
}

.gs-inner-textual {
    margin: 0 auto math-clamp(75, 108);
    
    &__container {
        max-width: rem(800);
    }
    
    & > *:last-child {
        margin-bottom: 0;
    }
    
    hr {
        display: block;
        margin: 0 0 math-clamp(28, 33);
        color: $grey;
        height: rem(2);
    }
    
    h3 {
        padding: 0;
    }
    
    h4 {
        padding: 0;
        color: $coregreen;
    }
    
    h5,
    h6 {
        margin-bottom: rem(13);
        padding: 0;
        font-weight: 700;
        font-size: rem(18);
        color: $black;
    }
    
    img {
        margin: rem(19) 0 math-clamp(36, 52);
        border-radius: rem(4);
    }
    
    &__bot {
        padding-top: rem(53);
        border-top: rem(2) solid $grey;
    }
}

[class*=_is-preview] {
    visibility: visible !important;
    
    * {
        visibility: visible !important;
        opacity: 1 !important;
    }
}

.gs-blog-share {
    padding-top: math-clamp(20, 40);
    padding-bottom: math-clamp(25, 45);
    border-top: 2px solid $grey;
    margin-top: math-clamp(50, 60);
}

.hs_error_rollup {
    display: none !important;
}

.gs-error-404 {
    padding: math-clamp(60, 150) 0;
    text-align: center;
    
    h1 {
        font-size: math-clamp(50, 100);
    }
}

.single-post .main-wrapper {
    h2, .h2 {
        @extend %h3;
    }
}
