.gs-image-text-section-dark-background {
    $cd: &;
    
    position: relative;
    margin-bottom: math-clamp(111, 115);
    
    &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: math-clamp(90, 100);
        background-color: $white;
    }
    
    &__start {
        width: 48%;
        @media (max-width: 767px) {
            width: 100%;
            order: 1;
        }
    }
    
    &__end {
        display: flex;
        justify-content: flex-end;
        width: 48%;
        @media (max-width: 767px) {
            justify-content: flex-start;
            width: 100%;
        }
    }
    
    &__top {
        padding: math-clamp(59, 111) 0 0;
        background-color: $coregreen;
    }
    
    &__bot {
        position: relative;
        background-color: $lightgrey;
        
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: math-clamp(162, 365);
            background-color: $coregreen;
        }
        
        &:after {
            content: '';
            position: absolute;
            top: rem(-5);
            width: 100%;
            height: rem(10);
            background-color: $coregreen;
        }
    }
    
    &__content {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
    }
    
    &__title {
        margin-bottom: 0;
        color: $white;
    }
    
    &__textual {
        padding-bottom: math-clamp(80, 120);
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: rem(523);
        
        & > *:nth-child(n+2) {
            margin-top: rem(34);
        }
    }
    
    &__text {
        font-weight: 400;
        font-size: math-clamp(14, 16);
        color: $black;
        line-height: 1.6;
        
        &_white {
            color: $white;
        }
    }
    
    &__small-poster {
        position: relative;
        max-width: 100%;
        width: 550px;
        height: 550px;
        border-radius: math-clamp(2, 4) math-clamp(86, 150) math-clamp(2, 4) math-clamp(2, 4);
        overflow: hidden;
        
        @media (max-width: 767px) {
            width: auto;
            height: auto;
            
            &::after {
                content: '';
                display: block;
                padding-bottom: 100%;
            }
            
            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    
    &__big-poster {
        position: relative;
        width: 100%;
        height: math-clamp(350, 700);
        border-radius: 0 math-clamp(207, 500) math-clamp(207, 500) 0;
        overflow: hidden;
    }
    
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
    }
    
    &__poster {
        position: relative;
        width: math-clamp(315, 550);
        max-width: 100%;
        border-radius: 0 math-clamp(75, 150) 0 0;
        overflow: hidden;
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
    }
    
    &__main {
        position: relative;
        padding: 0 0 math-clamp(50, 69);
        background-color: $lightgrey;
        overflow: hidden;
        @media (max-width: 575px) {
            margin-top: -40px;
        }
        
        #{$cd} {
            &__content {
                padding-top: math-clamp(100, 110);
            }
        }
    }
    
    &__circle {
        position: absolute;
        top: 0;
        right: 0;
        width: 120%;
        transform: translateX(50%);
        @media (max-width: 575px) {
            top: 0;
            right: unset;
            left: 0;
            transform: translate(0);
            width: 929px;
        }
        
        &::after {
            content: '';
            display: block;
            padding-bottom: 100%;
            border-radius: 50%;
            background-color: $white;
        }
    }
    
    &__list {
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        width: 100%;
        
        & > *:nth-child(n+2) {
            margin-top: rem(80);
            @media (max-width: 767px) {
                margin-top: 100px;
            }
        }
    }
    
    &__item {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 100%;
        
        &:nth-child(even) {
            flex-direction: row-reverse;
            
            #{$cd} {
                &__poster {
                    border-radius: 0 0 0 math-clamp(75, 150);
                }
                
                &__end {
                    justify-content: flex-start;
                    @media (max-width: 767px) {
                        justify-content: flex-start;
                    }
                }
            }
        }
        
        & > *:nth-child(n+2) {
            @media (max-width: 767px) {
                margin-top: 49px;
            }
        }
    }
    
    &__item-textual {
        max-width: rem(456);
        
        #{$cd} {
            &__text {
                margin-top: math-clamp(12, 17);
            }
            
            &__btn {
                margin-top: math-clamp(24, 34);
            }
        }
    }
    
    &__name {
        margin: 0;
        padding: 0;
    }
    
    &_with-big-img {
        margin-bottom: 0;
        
        &::before {
            display: none;
        }
        
        #{$cd} {
            &__start {
                @media (max-width: 767px) {
                    order: 0;
                }
            }
            
            &__textual {
                max-width: rem(456);
                
                @media (max-width: 767px) {
                    padding-bottom: 50px;
                }
            }
        }
    }
    
    &_background-transparent {
        #{$cd} {
            &__bot {
                background-color: transparent;
            }
        }
    }
}
