.gs-footer {
    padding: math-clamp(43, 78) 0 math-clamp(0, 22);
    background-color: $black;
    color: $white;
    @media (max-width: 767px) {
        padding-left: 0;
        padding-right: 0;
    }
    
    a {
        @extend %transition;
        color: $white;
        text-decoration: none;
        
        @media (hover: hover) {
            &:hover {
                color: $salmon;
            }
        }
    }
    
    &__top {
        margin-bottom: math-clamp(29, 31);
        padding-bottom: math-clamp(29, 23);
        border-bottom: rem(2) solid $coregreen;
        @media (max-width: 767px) {
            margin-bottom: 29px;
            padding-bottom: 29px;
        }
    }
    
    &__logo {
        @extend %logo;
        
        img,
        svg {
            width: rem(223);
        }
    }
    
    &__mid {
        display: flex;
        justify-content: space-between;
        padding-bottom: rem(48);
        min-height: rem(306);
        @media (max-width: 575px) {
            flex-direction: column;
        }
        
        & > *:nth-child(n+2) {
            margin-left: rem(46);
            @media (max-width: 575px) {
                margin-left: 0;
                margin-top: 46px;
            }
        }
    }
    
    &__socials {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;
        padding: 0;
        margin: 0 0 math-clamp(59, 65);
        list-style: none;
        
        & > * {
            margin-right: rem(23);
            
            &:last-child {
                margin-right: 0;
            }
        }
        
        a {
            text-decoration: none;
            
            @media (hover: hover) {
                &:hover {
                    path {
                        fill: $salmon;
                    }
                }
            }
        }
        
        svg {
            max-width: rem(19);
            height: auto;
            
            path {
                @extend %transition;
            }
        }
    }
    
    &__title {
        margin-bottom: rem(21);
        font-family: $font-family-lft-etica;
        font-weight: 700;
        font-size: rem(16);
        line-height: 1.3;
    }
    
    &__end {
        display: flex;
        flex-wrap: wrap;
        
        & > * {
            margin-right: rem(80);
            margin-bottom: rem(80);
            
            &:last-child {
                margin-right: 0;
                margin-bottom: 0;
            }
        }
    }
    
    &__list {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        list-style: none;
        
        & > *:nth-child(n+2) {
            margin-top: rem(15);
        }
        
        a {
            font-weight: 350;
            font-size: rem(16);
            line-height: 1;
            text-decoration: none;
        }
    }
    
    &__copyright {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding-top: rem(18);
        font-weight: 400;
        font-size: math-clamp(10, 12);
        // gap: 30px;
        line-height: 1;
        border-top: rem(2) solid $coregreen;
        
        & > * {
            margin-bottom: rem(30);
        }
        
        a {
            text-decoration: underline;
        }
    }
    
    &__text {
        max-width: rem(650);
        line-height: 1.6;
        
        @media (max-width: 991px) {
            max-width: unset;
            order: 1;
            width: 100%;
        }
        
        p {
            margin-bottom: 0;
        }
    }
    
    &__developer {
        text-decoration: none !important;
    }
}

